.mzb-banner-posts,
.mzb-grid-module,
.mzb-featured-posts,
.mzb-post-list,
.mzb-featured-categories {

	.mzb-post {

		.mzb-featured-image {

			&.zoomOut {
				overflow: hidden;

				img {
					transition: 310ms;
					transform: scale(1.2);
				}
			}

			&.zoomIn {
				overflow: hidden;

				img {
					transition: 310ms;
					transform: scale(1);
				}
			}

			&.rotateLeft,
			&.rotateRight {
				overflow: hidden;
			}

			&.rotateLeft {

				img {
					transition: 310ms;
					transform: rotate(0) scale(1);
				}
			}

			&.rotateRight {

				img {
					transition: 310ms;
					transform: rotate(0) scale(1);
				}
			}

			&.slideLeft {
				overflow: hidden;

				img {
					transition: 310ms;
					transform: translateX(8px) scale(1.15);
				}
			}

			&.slideRight {
				overflow: hidden;

				img {
					transition: 310ms;
					transform: translateX(-8px) scale(1.15);
				}
			}
		}

		&:hover {

			.mzb-featured-image {
	
				&.zoomOut {
		
					img {
						transform: scale(1);
					}
				}

				&.zoomIn {
		
					img {
						transform: scale(1.2);
					}
				}

				&.opacity {

					img {
						opacity: 0.8;
						background-color: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
					}
				}

				&.rotateLeft {

					img {
						transform: rotate(5deg) scale(1.2);
					}
				}

				&.rotateRight {

					img {
						transform: rotate(-5deg) scale(1.2);
					}
				}

				&.slideLeft {

					img {
						transform: translateX(-8px) scale(1.15);
					}
				}

				&.slideRight {

					img {
						transform: translateX(8px) scale(1.15);
					}
				}
			}
		}
	}
}
