.mzb-post-video {
	.mzb-posts {
		display: grid;
		grid-template-columns: repeat(var(--grid-column, 3), 1fr);
		grid-template-rows: 1fr;
		gap: 20px;
		justify-items: center;
		overflow: hidden;

		.mzb-post {

			&:not(.mzb-first-video--highlight) {
				.mzb-post-content {
					display: flex;
					flex-direction: column;
					justify-content: center;
				}
			}

			position: relative;
			display: flex;
			flex-direction: column;
			// flex-wrap: wrap;
			width: 100%;

			.mzb-video {
				position: relative;
				border-radius: var(--mzb-border-radius--xs);
				// background-color: #626262;
				// filter: drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.1));
				overflow: hidden;
				width: 100%;
				height: 100%;
			}

			&-content {

				.mzb-post-title a,
				.mzb-read-more a,
				.mzb-post-meta a {
					pointer-events: auto !important;
				}

				.mzb-post-title {
					margin-top: 10px;
					margin-bottom: unset;
				}
			}

			&-entry-meta {
				&.mzb-meta-separator--circle {
					span:nth-child(odd):not(:last-child)::after {
						content: "●";
						font-size: 0.5em;
						vertical-align: middle;
					}
				}

				&.mzb-meta-separator--square {
					span:nth-child(odd):not(:last-child)::after {
						content: "■";
						font-size: 0.5em;
						vertical-align: middle;
					}
				}

				&.mzb-meta-separator--vertical-dash {
					span:nth-child(odd):not(:last-child)::after {
						content: "|";
						color: #999;
					}
				}

				&.mzb-meta-separator--horizontal-dash {
					span:nth-child(odd):not(:last-child)::after {
						content: "–";
						color: #999;
					}
				}

				&.mzb-meta-separator--outline-circle {
					span:nth-child(odd):not(:last-child)::after {
						content: "○";
						font-size: 0.6em;
						vertical-align: middle;
					}
				}
			}

			&-title {

				a {
					font-size: 16px;
					font-weight: var(--mzb-font-weight--semibold);
					line-height: 1.2;
					display: block;
					overflow: hidden;
					text-overflow: ellipsis;
					text-decoration: none;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
			}

			&-categories {
				display: inline-flex;
				gap: 10px;
				flex-wrap: wrap;

				a {
					background-color: var(--color--primary);
					padding: 4px 8px;
					border-radius: var(--mzb-border-radius--xxl);
					color: var(--color--white);
					font-weight: var(--mzb-font-weight--semibold);
					font-size: 12px;
					display: inline-block;
					line-height: 14px;
					text-decoration: none;
				}

				&.mzb-large {
					a {
						padding: 17px 25px;
					}
				}

				&.mzb-small {
					a {
						padding: 8px 16px;
					}
				}

				&.mzb-medium {
					a {
						padding: 12px 20px;
					}
				}

				&.mzb-style-outline {
					a {
						background: transparent;
						border: 1px solid var(--color--primary);
					}
				}

				&.mzb-style-plain {
					a {
						background-color: transparent;
						padding: 0;
					}
				}

				.editor-styles-wrapper {
					a {
						color: var(--color--white);
					}
				}
			}

			&-date {
				display: flex;
				gap: 5px;
				align-items: center;

				a {
					display: block;
					font-weight: 400;
					color: #414141;
					font-size: 14px;
					text-decoration: none;

					&:hover {
						opacity: 0.8;
						color: var(--color--primary);
					}
				}

				svg {
					fill: #414141;
				}
			}

			&-author {
				display: flex;
				gap: 5px;
				align-items: center;

				a {
					display: block;
					font-weight: 400;
					color: #414141;
					font-size: 14px;
					text-decoration: none;

					&:hover {
						opacity: 0.8;
						color: var(--color--primary);
					}
				}

				img {
					color: #414141;
				}
			}

			.mzb-post-entry-meta {
				display: flex;
				align-items: center;
				gap: 8px;
				flex-wrap: wrap;
				margin-top: 10px;
			}

		}

		.mzb-image-overlay {
			height: 100%;
		}

		.mzb-featured-image {
			height: 100%;
			object-fit: cover;
			width: 100%;
		}

		.mzb-video-player {
			object-fit: cover;
			height: 100%;
			width: 100%;
		}

		.mzb-custom-embed-play {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: var(--color--white);
			border-radius: 50%;
			height: 30px;
			width: 30px;
			display: flex;
			align-items: center;
			justify-content: center;
			z-index: 2;

			svg {
				height: 8px;
				width: 8px;
				fill: var(--color--primary);
			}
		}

		.mzb-entry-summary {
			margin-top: 12px;
		}

		.mzb-read-more {
			margin-top: 12px;

			a {
				color: #414141;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.3;
				text-transform: capitalize;
				text-decoration: none;
				opacity: 0.9;
			}
		}
	}

	&.mzb-preset-style-1 {
		.mzb-posts {
			grid-template-columns: 1.4fr 1fr;
			gap: 20px;

			.mzb-post {
				&:not(.mzb-first-video--highlight){
					-moz-column-gap: 16px;
					column-gap: 16px;
					flex-direction: row;
				}

				&.mzb-first-video--highlight {
					display: block;
					grid-column-start: 1;
					grid-column-end: 2;
					grid-row-start: 1;
					grid-row-end: 5;
					padding: 0;

					.mzb-video {
						width: 100%;
						height: 100%;
						position: relative;
						display: inline-block;

						.mzb-featured-image {
							display: block;
							width: 100%;
							height: 100%;
						}

						.mzb-image-overlay {

							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background: linear-gradient(180deg,
									rgba(255, 255, 255, 0) 0%,
									rgba(0, 0, 0, 0.6) 100%);
							z-index: 1;

						}
					}

					.mzb-post-content {
						position: absolute;
						left: 10px;
						right: 0;
						bottom: 10px;
						// background: linear-gradient(180deg,
						// 		rgba(255, 255, 255, 0) 0%,
						// 		rgba(0, 0, 0, 0.6) 100%);
						mix-blend-mode: normal;
						// padding: 15px;
						color: var(--color--white);
						display: grid;
						align-items: end;
						grid-template-rows: repeat(1, 1fr);
						height: 100%;
						z-index: 2;

						.mzb-post-title a,
						.mzb-read-more a,
						.mzb-post-meta a {
							pointer-events: auto !important;
						}

						.mzb-post-title {
							margin-top: 10px;
							margin-bottom: unset;
						}

						svg {
							height: 14px;
							width: 14px;
							fill: var(--color--white);
						}

						.mzb-post-author a,
						.mzb-post-date a,
						.mzb-read-more a {
							color: #fff;
						}
					}
				}
			}

			@media (max-width: 768px) {
				grid-template-columns: 1fr;

				.mzb-post {

					&.mzb-first-video--highlight {

						.mzb-post-title {

							a {
								font-size: 18px;
							}
						}
					}

					.mzb-post-content{
						flex: 1;
					}
				}
			}

			@media (max-width: 420px) {

				.mzb-post {
					&:not(.mzb-first-video--highlight){
						flex-direction: column;

						.mzb-video {
							width: 100%;
						}

						.mzb-post-content{
							width: 100%;
						}
					}
				}

			}

		}
	}

	&.mzb-preset-style-2 {
		.mzb-posts {
			grid-template-columns: 1.4fr 1fr 1fr;
			gap: 20px;

			.mzb-post {
				flex-direction: column;
				-moz-column-gap: 16px;
				column-gap: 16px;

				.mzb-video {
					width: 100%;
					height: 100%;
					position: relative;
					display: inline-block;

					.mzb-featured-image {
						display: block;
						width: 100%;
						height: auto;
					}

					.mzb-image-overlay {

						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: linear-gradient(180deg,
								rgba(255, 255, 255, 0) 0%,
								rgba(0, 0, 0, 0.6) 100%);
						z-index: 1;

					}
				}

				&.mzb-first-video--highlight {
					grid-column-start: 1;
					grid-column-end: 2;
					grid-row-start: 1;
					grid-row-end: 5;
					padding: 0;
					flex-direction: row;

					.mzb-video {
						width: 100%;
						height: 100%;
					}

					.mzb-post-content {
						position: absolute;
						left: 10px;
						right: 0;
						bottom: 10px;
						// background: linear-gradient(180deg,
						// 		rgba(255, 255, 255, 0) 0%,
						// 		rgba(0, 0, 0, 0.6) 100%);
						mix-blend-mode: normal;
						// padding: 15px;
						color: var(--color--white);
						display: grid;
						align-items: end;
						grid-template-rows: repeat(1, 1fr);
						height: 100%;
						z-index: 2;

						.mzb-post-title a,
						.mzb-read-more a,
						.mzb-post-meta a {
							pointer-events: auto !important;
						}

						.mzb-post-title {
							margin-top: 10px;
							margin-bottom: unset;
						}

						svg {
							height: 14px;
							width: 14px;
							fill: var(--color--white);
						}

						.mzb-post-author a,
						.mzb-post-date a,
						.mzb-read-more a {
							color: #fff;
						}
					}
				}
			}

			@media (max-width: 768px) {
				grid-template-columns: 1fr;

				.mzb-post {

					&.mzb-first-video--highlight {

						.mzb-post-title {

							a {
								font-size: 18px;
							}
						}
					}
				}
			}

		}
	}

	&.mzb-preset-style-3 {
		.mzb-posts {
			grid-template-columns: 2fr 1fr;
			grid-template-areas:
				"highlighted post-1"
				"highlighted post-2";
			gap: 20px;

			.mzb-post {

				&.mzb-first-video--highlight {
					display: block;
					grid-area: highlighted;
					padding: 0;

					.mzb-video {
						width: 100%;
						height: 100%;
						position: relative;
						display: inline-block;

						.mzb-featured-image {
							display: block;
							width: 100%;
							height: 100%;
						}
					}

					.mzb-post-content {
						position: absolute;
						left: 0;
						right: 0;
						bottom: 0;
						padding: 10px;
						mix-blend-mode: normal;
						color: var(--color--white);
						display: grid;
						align-items: end;
						grid-template-rows: repeat(1, 1fr);
						height: 100%;
						z-index: 2;

						.mzb-post-title a,
						.mzb-read-more a,
						.mzb-post-meta a {
							pointer-events: auto !important;
						}

						.mzb-post-title {
							margin-top: 10px;
							margin-bottom: unset;
						}

						svg {
							height: 14px;
							width: 14px;
							fill: var(--color--white);
						}

						.mzb-post-author a,
						.mzb-post-date a,
						.mzb-read-more a {
							color: #fff;
						}
					}
				}

				&:nth-child(2){
					grid-area: post-1;
				}

				&:nth-child(3){
					grid-area: post-2;
				}

				&:not(.mzb-first-video--highlight){

					.mzb-post-content{
						position: absolute;
						left: 0;
						right: 0;
						bottom: 0;
						padding: 10px;
						z-index: 2;
					}
				}

				.mzb-video {
					.mzb-image-overlay {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: linear-gradient(180deg,
								rgba(255, 255, 255, 0) 0%,
								rgba(0, 0, 0, 0.6) 100%);
						z-index: 1;
					}
				}
			}

			&.mzb-post-col--full{
				grid-template-columns: 1fr;
			}

			@media (max-width: 768px) {
				grid-template-areas:
				"highlighted highlighted"
				"post-1 post-2";
				grid-template-columns: 1fr 1fr;

				.mzb-post {

					&.mzb-first-video--highlight {

						.mzb-post-title {

							a {
								font-size: 18px;
							}
						}
					}
				}
			}

			@media (max-width: 520px) {
				grid-template-areas:
				"highlighted"
				"post-1"
				"post-2";
				grid-template-columns: 1fr;

				.mzb-post {

					&.mzb-first-video--highlight {

						.mzb-post-title {

							a {
								font-size: 18px;
							}
						}
					}
				}
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-post-video {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:nth-child(odd):not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}
