.mzb-grid-module {
	&.mzb-layout-2 {
		.mzb-post {
			all: unset;
			z-index: 1;
		}

		.mzb-post-title {
			a {
				color: var(--color--black);
				text-shadow: unset;

				&:hover {
					opacity: unset;
					color: var(--color--primary);
				}
			}
		}

		&.mzb-layout-2-style-1 {
			.mzb-posts {
				&.mzb-post-col--2 {
					grid-template-columns: repeat(2, 1fr);
				}

				&.mzb-post-col--3 {
					grid-template-columns: repeat(3, 1fr);
				}
			}

			.mzb-featured-image {
				&.mzb-category--inside-image {
					position: relative;

					.mzb-post-meta {
						position: absolute;
						left: 0;
						bottom: 0;
						right: 0;
						margin: 20px;
					}
				}
			}

			.mzb-post-content {
				all: unset;
				display: grid;
				padding: 12px 0;
				gap: 12px;

				.mzb-post-view-count,
				.mzb-post-read-time {
					svg {
						fill: var(--color--black);
					}
				}

				.mzb-read-more {
					color: var(--color--black);
				}
			}
		}

		.mzb-post-entry-meta {

			a {
				color: var(--color--black);

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		.mzb-post-date {

			svg {
				fill: var(--color--black);
			}
		}
	}
}

@media(max-width: 768px) {
	.mzb-grid-module {

		&.mzb-layout-2 {

			&.mzb-layout-2-style-1 {
				.mzb-posts {
					&.mzb-post-col--2 {
						grid-template-columns: 1fr;
					}

					&.mzb-post-col--3 {
						grid-template-columns: 1fr;
					}
				}
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-grid-module {

		&.mzb-layout-2 {

			&.mzb-layout-2-style-1 {
				.mzb-posts {
					&.mzb-post-col--2 {
						grid-template-columns: repeat(2, 1fr);
					}

					&.mzb-post-col--3 {
						grid-template-columns: repeat(3, 1fr);
					}
				}

				.mzb-post-content {
					all: unset;
					display: grid;
					padding: 12px 0;
					gap: 12px;
				}
			}
		}
	}
}

@media (min-width: 1200px) {

	.mzb-grid-module {

		&.mzb-layout-2 {
			&.mzb-layout-2-style-1 {
				.mzb-posts {
					&.mzb-post-col--2 {
						grid-template-columns: repeat(2, 1fr);
					}

					&.mzb-post-col--3 {
						grid-template-columns: repeat(3, 1fr);
					}
				}

				.mzb-post-content {
					all: unset;
					display: grid;
					padding: 12px 0;
					gap: 12px;
				}
			}
		}
	}
}