@import './layout-1';
@import './layout-2';
@import './editor.scss';

.mzb-latest-posts {

	.mzb-posts {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	.mzb-featured-image {

		img {
			display: block;
			height: 66.35vh;
			width: 100%;
			transform: scale(1);
			transition: 0.3s transform ease-in-out;
			object-fit: cover;
		}
	}

	.mzb-post {
		position: relative;
		height: 100%;
		overflow: hidden;
		height: 66.35vh;

		&-content {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
			mix-blend-mode: normal;
			padding: 15px;
			color: var(--color--white);
			display: grid;
			align-items: end;
			grid-template-rows: repeat(1, 1fr);
			height: 100%;

			.mzb-post-title {
				margin-bottom: 10px;
			}
		}


		&-title {

			a {
				display: block;
				color: var(--color--white);
				font-size: 24px;
				font-weight: var(--mzb-font-weight--semibold);
				text-shadow: 1px 1px 2px rgb(0 0 0 / 20%);
				line-height: 1.2;
				overflow: hidden;
				text-overflow: ellipsis;
				text-decoration: none;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}

		.mzb-entry-header {
			padding: 30px;

			p {
				font-size: 32px;
			}
		}

		&-date {
			a {
				font-size: 14px;
				color: var(--color--white);

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}

		&-author {
			a {
				max-width: 100px;
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				font-weight: var(--mzb-font-weight--regular);
				text-decoration: none;
			}

			svg {
				height: 14px;
				width: 14px;
				fill: var(--color--white);
			}
		}

		&-entry-meta {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 8px;
		}


		.mzb-read-more {
			margin-top: 12px;

			a {
				color: #fff;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.3;
				text-transform: capitalize;
				text-decoration: none;
				opacity: 0.9;

				&:hover {
					color: var(--color--primary);
				}
			}
		}
	}

	.mzb-featured-image+.mzb-post-content {
		background: linear-gradient(0deg,
				rgba(0, 0, 0, 0.7),
				rgba(0, 0, 0, 0) 60%,
				rgba(0, 0, 0, 0));
		height: 100%;
	}
}

.mzb-section {

	.mzb-latest-posts {

		.mzb-posts {
			margin-right: 0;
			margin-left: 0;
		}
	}
}

@media (max-width: 768px) {

	.mzb-latest-posts {

		.mzb-posts {
			grid-template-columns: 1fr;
		}
	}
}

@media (min-width: 1200px) {

	.mzb-latest-posts {

		.mzb-posts {
			grid-template-columns: repeat(4, 1fr);
			grid-template-rows: 1fr;

			&.mzb-post-col--2 {
				grid-template-columns: repeat(2, 1fr);
			}
		}
	}
}
