.mzb-date-weather {
	display: flex;
	align-items: center;

	.mzb-temperature {
		font-weight: var(--mzb-font-weight--bold);
		font-size: 14px;
		margin: 0 5px 0 10px;
	}

	.mzb-weather-icon {
		display: flex;
	}

	.mzb-weather-date {
		font-weight: var(--mzb-font-weight--regular);
		font-size: 13px;
	}

	.mzb-weather-icon {
		svg {
			fill: var(--color--primary);
			width: 24px;
			height: 24px;
		}
	}
}
