.mzb-category-list {

	.mzb-posts {

		&.mzb-layout-2 {

			&.mzb-layout-2-style-1 {

				.mzb-post-categories {

					a {
						text-align: center;
						display: flex;
						align-items: center;
						justify-content: center;
					}
				}
			}

			&.mzb-layout-2-style-1 {
				grid-template-columns: 1fr 1fr 1fr;

				.mzb-post {
					height: 100px;
				}
			}

			.mzb-post-count-wrapper {
				display: none;
			}
		}
	}
}
