.mzb-featured-posts {
    .mzb-layout-4 {
        &.mzb-layout-4-style-1 {

            &.mzb-post-col--1,
            &.mzb-post-col--2,
            &.mzb-post-col--3,
            &.mzb-post-col--4 {
                display: block !important;
                margin: -10px;

                .mzb-post {
                    display: inline-block;
                    vertical-align: top;
                    padding: 10px;
                    margin: 0;
                    box-sizing: border-box;

                    .mzb-featured-image {

                        img {
                            width: 100%;
                            height: auto;
                            display: block;
                        }
                    }

                    .mzb-post-content {
                        padding-top: 15px;
                    }
                }
            }

        }
    }
}

@mixin layout-style($top, $bottom) {
	.mzb-featured-posts {
		.mzb-layout-4 {
			&.mzb-layout-4-style-1 {
				&.mzb-layout-4-style-1-top-row-#{$top}.mzb-layout-4-style-1-bottom-row-#{$bottom} {
					.mzb-post {
						// Top row
						@for $i from 1 through $top {
							&:nth-child(#{$i}) {
								width: #{100% / $top};
							}
						}

						// Bottom rows: All posts after the top ones
						&:nth-child(n+#{$top + 1}) {
							width: #{100% / $bottom};
						}

						// Responsive styles for tablets
						@media (max-width: 767px) {
							&:nth-child(-n+3),
							&:nth-child(n+4):nth-child(-n+7),
							&:nth-child(n+8) {
								width: 50%;
							}
						}

						// Responsive styles for mobile
						@media (max-width: 480px) {
							&:nth-child(-n+3),
							&:nth-child(n+4):nth-child(-n+7),
							&:nth-child(n+8) {
								width: 100%;
							}
						}
					}
				}
			}
		}
	}
}

@for $top from 1 through 3 {
	@for $bottom from 1 through 4 {
		@include layout-style($top, $bottom);
	}
}
