.mzb-featured-posts {

	.mzb-layout-5 {

		.mzb-post {
			column-gap: 16px;
			align-items: center;

			&.mzb-first-post--highlight {
				display: block;
				position: relative;
				grid-column-start: 1;
				grid-column-end: 2;
				padding: 0;
				align-items: normal;
				height: 500px;
				box-sizing: content-box;

				.mzb-post-meta {
					display: flex;
					z-index: 2;
				}

				.mzb-featured-image {
					height: 500px;
					position: relative;

					img {
						width: 100%;
						height: 100%;
					}

					&::before {
						border-radius: 4px;
						background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 35.1%, #000 99.92%);
						position: absolute;
						content: '';
						top: 0;
						left: 0;
						z-index: 1;
						height: 100%;
						width: 100%;
					}
				}

				.mzb-post-content {
					display: block;
					position: absolute;
					bottom: 0;
					left: 0;
					right: 0;
					z-index: 9;
					padding: 20px;
				}

				.mzb-post-author {
					display: flex;

					a{
						color: white;
					}
				}

				.mzb-post-date{
					a{
						color: white;
					}

					svg{
						fill: white;
					}
				}
			}
		}

		&.mzb-layout-5-style-1 {
			grid-template-columns: 1fr;
			--column-gap: 20px;
			--row-gap: 20px;
			column-gap: var(--column-gap);
			row-gap: var(--row-gap);

			.mzb-featured-image {
				flex: unset;

				img {
					height: 160px;
					width: 100%;
					border-radius: 4px;
				}
			}

			.mzb-post-content {
				padding: 0;

				.mzb-post-title {
					margin-top: 0;
					margin-bottom: 12px;
				}
			}

			.mzb-post-author {
				display: none;
			}
		}
	}
}

@media (min-width: 500px) {
	.mzb-featured-posts {

		.mzb-layout-5 {

			&.mzb-layout-5-style-1 {
				.mzb-post {
					&:not(.mzb-first-post--highlight) {
						flex-direction: row;
						column-gap: 20px;
						align-items: center;

						.mzb-featured-image{
							width: auto;

							img {
								height: 160px;
								width: 210px;
							}
						}

						.mzb-post-content{
							align-self: center;
						}
					}
				}
			}

		}
	}
}


@media (min-width: 992px) {

	.mzb-featured-posts {

		.mzb-layout-5 {

			&.mzb-layout-5-style-1 {
				grid-template-columns: 1.4fr 1fr;
			}

			&.mzb-layout-5-style-1 {

				.mzb-post {

					&.mzb-first-post--highlight {
						grid-column: 1;
						grid-row: span 3;
						padding: 0;
					}

					&:not(.mzb-first-post--highlight) {
						grid-column: 2;
					}
				}
			}
		}
	}
}
