.mzb-social-icons {
	display: flex;
	gap: 10px;
	align-items: center;

	.mzb-social-links {
		display: flex;
		gap: 10px;
		align-items: center;
		justify-content: center;

		.block-editor-block-list__layout {
			display: flex;
			gap: 10px;
		}

		.mzb-social-icons-insert {

			button {
				display: flex;
				padding: 9px;
				margin-bottom: 0;
				background-color: var(--color--light--primary);
				border-radius: 50%;
				justify-content: center;
				align-items: center;
				border-color: unset;
				border-style: unset;
	
				svg {
					fill: var(--color--white);
					width: 14px;
					height: 14px;
				}
	
				&:hover {
					background-color: var(--color--primary);
	
					svg {
						fill: var(--color--white);
					}
				}
			}
		}
	}
}

.mzb-social-icon {
	border: none;
	cursor: pointer;
	justify-content: center;
	align-items: center;
	display: flex;

	svg {
		fill: var(--color--primary);
		width: 14px;
		height: 14px;
	}

	a {
		display: flex;
		align-items: center;
		background-color: var(--color--light--primary);
		border-radius: 50%;
		padding: 9px;

		&:hover {
			background-color: var(--color--primary);

			svg {
				fill: var(--color--white);
			}
		}
	}
}

.edit-post-visual-editor .components-button {
	padding: 4px !important;
}

.components-popover__content {
	.magazine-blocks-url-input-head {
		margin-top: 20px;
	}
}
