/**
 * Featured posts.
 */

 .mzb-featured-posts {
	.mzb-border {
		.mzb-post {
			background-color: unset;
			border-radius: 0;
			padding: 0 0 20px 0;

			&:not(:last-child) {
				border-bottom: 1px dashed #BFBFBF;
			}

			.mzb-post-content {
				padding-bottom: 0;
			}

			&.mzb-first-post--highlight{
				padding: 0;

				.mzb-post-content {
					padding-top: 20px;
					padding-bottom: 20px;
				}
			}
		}

		&.mzb-layout-1-style-2 {
			.mzb-post-content {
				padding: 0;
			}
		}
	}

	.mzb-posts {
		&.mzb-border {
			column-gap: 20px;
		}
	}

	.mzb-layout-2 {
		&.mzb-border {
			.mzb-post {
				&.mzb-first-post--highlight {
					border-bottom: 0;

					.mzb-post-content {
						padding: 20px 0 0 0;
					}
				}
			}
		}

		&.mzb-layout-2-style-3 {
			&.mzb-border {
				gap: 0;

				.mzb-post {
					padding: 20px 0;

					&:not(:last-child) {
						border-bottom: 1px dashed #BFBFBF;
					}
				}
			}
		}
	}
}

/**
 * Featured categories.
 */
 .mzb-category-1-posts,
 .mzb-category-2-posts {
	 &.mzb-border {
		 .mzb-post {
			 background-color: unset;
			 border-radius: 0;
			 padding: 0 0 20px 0;

			 &:not(:last-child) {
				 border-bottom: 1px dashed #BFBFBF;
			 }
		 }

		 .mzb-post:nth-child(2){
			 padding: 0 0 20px 0;

			 .mzb-post-content {
				 padding: 20px 0 0 0;
			 }

			 .mzb-featured-image img {
				 border-radius: var(--mzb-border-radius--xs);;
			 }
		 }

		 .mzb-posts {
			 gap: 15px;
		 }
	 }
 }
