.mzb-pagination-numbers {
	display: flex;
	gap: 6px;
	margin-top: 32px;

	li {
		list-style: none;
	}

	.page-item {

		&.current {

			a {
				color: #fff;
				background-color: var(--color--primary);
			}
		}

		a {
			display: flex;
			align-items: center;
			justify-content: space-around;
			width: 32px;
			height: 32px;
			border-radius: 4px;
			border: 1px solid #f4f4f5;
			background-color: #fff;
			color: #3f3f46;
			font-size: 14px;
			padding: 8px;
			font-weight: 600;
		}

		svg {
			height: 16px;
			width: 16px;
		}
	}

	.css-13b06dz {
		justify-content: unset;
	}

	.components-flex {

		.components-button {
			display: flex;
			align-items: center;
			justify-content: space-around;
			width: 32px;
			height: 32px;
			border-radius: 4px;
			border: 1px solid #f4f4f5;
			background-color: #fff;
			color: #3f3f46;
			font-size: 14px;
			padding: 8px;
			font-weight: 600;
		}
	}
}
