.mzb-banner-posts {

	.mzb-posts {

		&.mzb-layout-1 {

			&.mzb-layout-1-style-2 {
				grid-template-columns: 1fr;
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {

			&.mzb-layout-1 {

				&.mzb-layout-1-style-2 {
					grid-template-columns: 2fr 1fr 1fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 3;
						grid-row-start: 1;
						grid-row-end: 3;
					}
				}
			}
		}
	}
}
