@import './layout-1';
@import './layout-2';
@import './editor.scss';

.mzb-grid-module {

	.mzb-top-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	.mzb-view-more {
		display: inline-block;

		>a {
			display: flex;
			align-items: center;
			gap: 5px;

			svg {
				fill: var(--color--primary);
			}
		}
	}

	.mzb-posts {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
	}

	.mzb-post-col--2 {
		grid-template-columns: repeat(2, 1fr);
	}

	.mzb-post-col--3 {
		grid-template-columns: repeat(3, 1fr);
	}

	.mzb-post-col--4 {
		grid-template-columns: repeat(4, 1fr);
	}

	.mzb-featured-image {
		position: relative;
		overflow: hidden;

		img {
			display: block;
			border-radius: inherit;
			height: 66.35vh;
			width: 100%;
			transform: scale(1);
			transition: 0.3s transform ease-in-out;
			object-fit: cover;
			background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
		}

		.mzb-overlay {
			position: absolute;
			inset: 0;
			background: linear-gradient(0deg,
					rgba(0, 0, 0, 0.7),
					rgba(0, 0, 0, 0) 60%,
					rgba(0, 0, 0, 0));
			z-index: 1;
			pointer-events: none;
			border-radius: inherit;
		}
	}

	.mzb-post {
		position: relative;
		height: 100%;
		height: 66.35vh;

		&-content {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			mix-blend-mode: normal;
			padding: 15px;
			color: var(--color--white);
			display: grid;
			align-items: end;
			grid-template-rows: repeat(1, 1fr);
			z-index: 2;
			gap: 12px;

			.mzb-post-title {
				z-index: 3;
				margin: 0;
			}

			.mzb-post-view-count,
			.mzb-post-read-time {
				display: flex;
				align-items: center;

				span {
					font-weight: var(--mzb-font-weight--regular);
					margin-left: 5px;
				}
			}

			svg {
				height: 14px;
				width: 14px;
			}

			.mzb-entry-content {
				.mzb-entry-summary {
					p {
						margin: 0;
					}
				}
			}
		}


		&-title {

			a {
				display: block;
				color: var(--color--white);
				font-size: 24px;
				font-weight: var(--mzb-font-weight--semibold);
				text-shadow: 1px 1px 2px rgb(0 0 0 / 20%);
				line-height: 1.2;
				overflow: hidden;
				text-overflow: ellipsis;
				text-decoration: none;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}

		&-entry-meta {

			.mzb-post-author,
			.mzb-post-date,
			.mzb-post-read-time {
				gap: 8px;
			}

			&.mzb-meta-separator--circle {
				span:not(:last-child)::after {
					content: "●";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--square {
				span:not(:last-child)::after {
					content: "■";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--vertical-dash {
				span:not(:last-child)::after {
					content: "|";
					color: #999;
				}
			}

			&.mzb-meta-separator--horizontal-dash {
				span:not(:last-child)::after {
					content: "–";
					color: #999;
				}
			}

			&.mzb-meta-separator--outline-circle {
				span:not(:last-child)::after {
					content: "○";
					font-size: 0.6em;
					vertical-align: middle;
				}
			}
		}

		.mzb-entry-header {
			padding: 30px;

			p {
				font-size: 32px;
			}
		}

		&-date {
			a {
				font-size: 14px;
				color: var(--color--white);

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}

		&-author {
			a {
				max-width: 100px;
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				font-weight: var(--mzb-font-weight--regular);
				text-decoration: none;
			}

			svg {
				height: 14px;
				width: 14px;
				fill: var(--color--white);
			}
		}

		&-entry-meta {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 8px;
			z-index: 3;
		}


		.mzb-read-more {
			margin-top: 12px;

			a {
				color: #fff;
				font-size: 16px;
				font-weight: 600;
				line-height: 1.3;
				text-transform: capitalize;
				text-decoration: none;
				opacity: 0.9;

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		&-meta {
			justify-content: space-between;
			flex-wrap: wrap;
			display: flex;
			gap: 10px;

			.comments-link {
				display: flex;
				gap: 5px;
				align-items: center;

				svg {
					height: 16px;
					width: 16px;
					fill: var(--color--primary);
				}
			}
		}
	}

	.mzb-featured-image+.mzb-post-content {
		// background: linear-gradient(0deg,
		// 		rgba(0, 0, 0, 0.7),
		// 		rgba(0, 0, 0, 0) 60%,
		// 		rgba(0, 0, 0, 0));
		height: 100%;
	}

	.mzb-posts {
		&.mzb-number-list {
			counter-reset: post-counter;

			&.mzb-number-list__with-image {

				&.mzb-number-list__circle {

					.mzb-post {
						.mzb-featured-image {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border-radius: 50%;
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__square {

					.mzb-post {
						.mzb-featured-image {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__dot {
					.mzb-post {
						.mzb-featured-image {
							&::before {
								content: counter(post-counter)".";
							}
						}
					}
				}

				&.mzb-number-list__zero {
					.mzb-post {
						.mzb-featured-image {
							&::before {
								content: counter(post-counter, decimal-leading-zero);
							}
						}
					}
				}

				.mzb-post {
					position: relative;

					.mzb-featured-image {

						&::before {
							counter-increment: post-counter;
							content: counter(post-counter);
							position: absolute;
							font-weight: bold;
							color: #000;
							height: 30px;
							width: 30px;
							z-index: 1;
						}
					}
				}
			}

			&.mzb-number-list__with-text {

				&.mzb-number-list__circle {

					.mzb-post {
						.mzb-post-content {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border-radius: 50%;
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__square {

					.mzb-post {
						.mzb-post-content {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__dot {
					.mzb-post {
						.mzb-post-content {
							&::before {
								content: counter(post-counter)".";
							}
						}
					}
				}

				&.mzb-number-list__zero {
					.mzb-post {
						.mzb-post-content {
							&::before {
								content: counter(post-counter, decimal-leading-zero);
							}
						}
					}
				}

				.mzb-post {
					position: relative;

					.mzb-post-content {

						&::before {
							counter-increment: post-counter;
							content: counter(post-counter);
							position: absolute;
							font-weight: bold;
							color: #000;
							height: 30px;
							width: 30px;
							z-index: 1;
						}
					}
				}
			}
		}

		&.mzb-post-col--4 {
			.mzb-post {
				&.mzb-post-separator {
					&:not(:nth-child(4n)) {
						border-right: 1px solid #e0e0e0;
						border-radius: 0;
					}
				}
			}
		}

		&.mzb-post-col--2 {
			.mzb-post {
				&.mzb-post-separator {
					&:nth-child(odd) {
						border-right: 1px solid #e0e0e0;
						border-radius: 0;
					}
				}
			}
		}
	}
}

.mzb-section {

	.mzb-grid-module {

		.mzb-posts {
			margin-right: 0;
			margin-left: 0;
		}
	}
}

.mzb-post-categories {
	display: flex;
	z-index: 3;
	gap: 12px;

	p {
		width: fit-content;
		border-radius: 4px;
	}
}

@media (max-width: 768px) {

	.mzb-grid-module {

		.mzb-posts {
			grid-template-columns: 1fr;
		}
	}
}

@media (min-width: 1200px) {

	.mzb-grid-module {

		.mzb-posts {
			grid-template-columns: repeat(4, 1fr);
			grid-template-rows: 1fr;

			&.mzb-post-col--2 {
				grid-template-columns: repeat(2, 1fr);
			}

			&.mzb-post-col--3 {
				grid-template-columns: repeat(3, 1fr);
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-grid-module {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}
