.mzb-post-list {

	.mzb-layout-2-style-1 {
		.mzb-post {
			flex-direction: column;
			align-items: center;
			padding: unset;
			overflow: hidden;
			border-radius: var(--mzb-border-radius--xs);
			gap: 0;

			.mzb-featured-image {
				margin-right: unset;
				width: 100%;
			}

			.mzb-post-content {
				padding: 15px;
				width: fit-content;
			}
		}
	}
}