@import './layout-1';
@import './layout-2';

.mzb-post-list {

	.mzb-category-in-image {
		position: relative;

		.mzb-post-meta {
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 1;
			padding: 5px;
			width: 100%;
			justify-content: space-between;
			box-sizing: border-box;
		}
	}

	.mzb-post-meta {
		justify-content: space-between;
		flex-wrap: wrap;
		display: flex;
		gap: 10px;
	}

	.mzb-top-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	.mzb-view-more {
		display: inline-block;

		>a {
			display: flex;
			align-items: center;
			gap: 5px;

			svg {
				fill: var(--color--primary);
			}
		}
	}

	.mzb-posts {
		display: grid;
		row-gap: 10px;
	}

	.mzb-featured-image {

		a {
			display: block;
		}
	}

	.mzb-post {
		display: flex;
		background-color: var(--color--white);
		border-radius: var(--mzb-border-radius--xs);
		gap: 15px;
		align-items: center;

		&.no-thumbnail {
			padding-inline: 8px;
		}

		&-content {
			flex-basis: 75%;
			// flex-grow: 1;
			align-self: center;

			.mzb-post-title {
				margin-top: 10px;
				margin-bottom: 8px;
			}

			.mzb-post-view-count,
			.mzb-post-read-time {
				display: flex;
				align-items: center;

				span {
					font-weight: var(--mzb-font-weight--regular);
					margin-left: 5px;
				}
			}

			svg {
				height: 14px;
				width: 14px;
				fill: #414141;
			}
		}

		&-entry-meta {
			flex-wrap: wrap;

			&.mzb-meta-separator--circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "●";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--square {
				span:nth-child(odd):not(:last-child)::after {
					content: "■";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--vertical-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "|";
					color: #999;
				}
			}

			&.mzb-meta-separator--horizontal-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "–";
					color: #999;
				}
			}

			&.mzb-meta-separator--outline-circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "○";
					font-size: 0.6em;
					vertical-align: middle;
				}
			}
		}

		img {
			height: 120px;
			min-width: 120px;
			max-width: 120px;
			border-radius: var(--mzb-border-radius--xs);
			display: block;
			object-fit: cover;
		}

		&-entry-meta {
			display: flex;
			justify-content: flex-start;
			gap: 8px;
		}

		&-title {

			a {
				font-size: 16px;
				font-weight: var(--mzb-font-weight--semibold);
				line-height: 1.2;
				overflow: hidden;
				text-decoration: none;
				text-decoration: none;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				font-size: 14px;
				font-weight: var(--mzb-font-weight--regular);
				color: #414141;
				text-decoration: none;

				&:hover {
					color: var(--color--primary);
				}
			}

			svg {
				fill: #414141;
			}
		}

		&.magazine-post--pos-left {
			grid-template-columns: repeat(3, 1fr);

			.magazine-post-info {
				grid-column: 2/4;
			}
		}

		&.magazine-post--pos-top {
			flex-direction: column;
			align-items: center;
			padding: unset;
			overflow: hidden;
			border-radius: var(--mzb-border-radius--xs);
			gap: 0;

			.mzb-featured-image {
				margin-right: unset;
				width: 100%;

				img {
					border-radius: unset;
					max-width: 100%;
					width: 100%;
					height: 150px;
				}
			}

			.mzb-post-content {
				padding: 15px;
				width: calc(100% - 30px);
			}
		}
	}

	.no-thumbnail {

		.mzb-post-content {
			padding-top: 7px;
			padding-bottom: 7px;
		}
	}

	.mzb-entry-summary {
		margin-top: 8px;
	}


	.mzb-read-more {
		margin-top: 12px;

		a {
			color: #3f3f46;
			font-size: 16px;
			font-weight: 600;
			line-height: 1.3;
			text-transform: capitalize;
			text-decoration: none;
			opacity: 0.9;
		}
	}

	.mzb-posts {
		&.mzb-number-list {
			counter-reset: post-counter;

			&.mzb-number-list__with-image {

				&.mzb-number-list__circle {

					.mzb-post {
						&::before {
							color: var(--color--white);
							background-color: var(--mzb-colors-primary);
							border-radius: 50%;
							border: 2px solid var(--color--white);
							padding: 5px;
							height: 30px;
							width: 30px;
							display: flex;
							align-items: center;
							justify-content: center;
						}
					}
				}

				&.mzb-number-list__square {

					.mzb-post {
						&::before {
							color: var(--color--white);
							background-color: var(--mzb-colors-primary);
							border: 2px solid var(--color--white);
							padding: 5px;
							height: 30px;
							width: 30px;
							display: flex;
							align-items: center;
							justify-content: center;
						}
					}
				}

				&.mzb-number-list__none {
					.mzb-post {
						&::before {
							content: counter(post-counter)".";
						}
					}
				}

				&.mzb-number-list__zero {
					.mzb-post {
						&::before {
							content: counter(post-counter, decimal-leading-zero);
						}
					}
				}

				.mzb-post {
					&::before {
						counter-increment: post-counter;
						content: counter(post-counter);
						position: absolute;
						font-weight: bold;
						color: #000;
						height: 30px;
						width: 30px;
						z-index: 1;
					}
				}
			}

			&.mzb-number-list__with-text {

				&.mzb-number-list__circle {

					.mzb-post {
						.mzb-post-content {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border-radius: 50%;
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__square {

					.mzb-post {
						.mzb-post-content {
							&::before {
								color: var(--color--white);
								background-color: var(--mzb-colors-primary);
								border: 2px solid var(--color--white);
								padding: 5px;
								height: 30px;
								width: 30px;
								display: flex;
								align-items: center;
								justify-content: center;
							}
						}
					}
				}

				&.mzb-number-list__none {
					.mzb-post {
						.mzb-post-content {
							&::before {
								content: counter(post-counter)".";
							}
						}
					}
				}

				&.mzb-number-list__zero {
					.mzb-post {
						.mzb-post-content {
							&::before {
								content: counter(post-counter, decimal-leading-zero);
							}
						}
					}
				}

				.mzb-post {
					.mzb-post-content {
						&::before {
							counter-increment: post-counter;
							content: counter(post-counter);
							position: absolute;
							font-weight: bold;
							color: #000;
							height: 30px;
							width: 30px;
							z-index: 1;
						}
					}
				}
			}
		}

		&.mzb-post-col--4 {
			.mzb-post {
				&.mzb-post-separator {
					&:not(:nth-child(4n)) {
						border-right: 1px solid #e0e0e0;
						border-radius: 0;
					}
				}
			}
		}

		&.mzb-post-col--2 {
			.mzb-post {
				&.mzb-post-separator {
					&:nth-child(odd) {
						border-right: 1px solid #e0e0e0;
						border-radius: 0;
					}
				}
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-post-list {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:nth-child(odd):not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}
