.mzb-advertisement {
	.mzb-advertisement-content {
		display: flex;
		flex-direction: column;
	}

	.mzb-advertisement-728x90 {
		img {
			width: 728px;
		}
	}

	.mzb-advertisement-160x600 {
		img {
			width: 160px;
		}
	}

	.mzb-advertisement-250x250 {
		img {
			width: 250px;
		}
	}

	.mzb-advertisement-468x60 {
		img {
			width: 468px;
		}
	}

	.mzb-advertisement-970x90 {
		img {
			width: 970px;
		}
	}

	img {
		object-fit: cover;
		display: block;
	}
}