.mzb-post-list {

	.mzb-layout-1-style-2 {
		&.mzb-post-col--2 {

			grid-template-columns: repeat(2, 1fr);
		}

		&.mzb-post-col--3 {

			grid-template-columns: repeat(3, 1fr);
		}

		&.mzb-post-col--4 {

			grid-template-columns: repeat(4, 1fr);
		}
	}

	.mzb-layout-1-style-3 {

		&:nth-child(even) {
			flex-direction: row-reverse;

			.mzb-post {
				display: grid;
				grid-template-columns: auto;
				align-items: center;
				position: relative;

				.mzb-featured-image {
					grid-column: 1;
					grid-row: 1;
				}

				.mzb-post-content {
					grid-column: 2;
					grid-row: 1;
					padding: 1.5rem;
				}

				&:nth-child(even) {
					.mzb-featured-image {
						grid-column: 2;
					}

					.mzb-post-content {
						grid-column: 1;
					}
				}

				.mzb-featured-image {
					img {
						width: 100%;
						height: auto;
						display: block;
					}
				}
			}

		}
	}
}

@media (max-width: 640px) {
	.mzb-posts {
		.mzb-post {
			flex-direction: column;

			.mzb-featured-image {
				width: 100%;
				img{
					width: 100%;
					max-width: 100%;
				}
			}
		}
	}
}

@media (max-width: 768px) {
	.mzb-posts {
		.mzb-post {
			grid-template-columns: 1fr;

			.mzb-featured-image,
			.mzb-post-content {
				grid-column: 1;
			}

			.mzb-featured-image {
				grid-row: 1;
			}

			.mzb-post-content {
				grid-row: 2;
			}

			&:nth-child(even) {
				.mzb-featured-image {
					grid-column: 1;
					grid-row: 1;
				}

				.mzb-post-content {
					grid-column: 1;
					grid-row: 2;
				}
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-post-list {

		.mzb-layout-1-style-2 {
			grid-template-columns: repeat(2, 1fr);
		}
	}
}

@media (min-width: 992px) {

	.mzb-post-list {

		.mzb-layout-1-style-2 {
			grid-template-columns: repeat(4, 1fr);
		}
	}
}
