.mzb-latest-posts {
	.mzb-layout-2 {
		.mzb-post {
			all: unset;
		}

		.mzb-post-title {
			a {
				color: var(--color--black);
				text-shadow: unset;

				&:hover {
					opacity: unset;
					color: var(--color--primary);
				}
			}
		}

		&.mzb-layout-2-style-1 {
			.mzb-post-content {
				all: unset;
				display: grid;
				padding: 24px 16px;
			}
		}

		.mzb-post-entry-meta {

			a {
				color: var(--color--black);

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		.mzb-post-date {

			svg {
				fill: var(--color--black);
			}
		}
	}
}