.mzb-section {
	position: relative;
	transition: all 200ms;

	&:has(.mzb-top-separator) {
		padding: 0;
		background-color: #eeeeee;
	}

	&:has(.mzb-bottom-separator) {
		padding: 0;
		background-color: #eeeeee;
	}

	.mzb-overlay {
		top: 0;
		bottom: 0;
		position: absolute;
		left: 0;
		right: 0;
	}
}

.mzb-container,
.mzb-container-fluid {
	margin-right: auto;
	margin-left: auto;
	position: relative;
}

.mzb-section-inner {
	display: flex;
	// flex-wrap: wrap;
	position: relative;

	@media (max-width: 62em) {
		flex-wrap: wrap;
	}

	&.mzb-height-fit-to-screen {
		min-height: 100vh;
	}

	.mzb-column {
		position: relative;
	}

	.mzb-column {
		padding-left: var(--colSpacing);
	}

	&.is-vertical {
		flex-direction: column;
	}

	&.is-horizontal {
		flex-direction: row;
	}

	&.justify-center {
		justify-content: center;
	}

	&.justify-left {
		justify-content: flex-start;
	}

	&.justify-left {
		justify-content: flex-end;
	}

	&.align-top {
		align-items: flex-start;
	}
	&.align-center {
		align-items: center;
	}
	&.align-bottom {
		align-items: flex-end;
	}
	&.align-stretch {
		align-items: stretch;
	}

}

.mzb-column-inner {
	transition: all 200ms;
}

.mzb-top-separator {
	display: block;
	width: 100%;
	height: 200px;
	overflow: hidden;

	.mzb-icon {
		width: 100%;
		height: 100%;
		transform: scaleX(1);
	}
}

.mzb-bottom-separator {
	display: block;
	width: 100%;
	height: 200px;
	overflow: hidden;
	position: relative;

	.mzb-icon {
		width: 100%;
		height: 100%;
		transform: scaleX(1);
		position: absolute;
		bottom: -1px;
	}
}
