.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
    .mzb-post-heading {
        &.mzb-heading-layout-2-style-1 {
            h2 {
                display: inline-block;
                background-color: var(--color--black);
                border-radius: 4px 4px 0px 0px;
                padding: 6px 12px;
                font-weight: 400;
                font-size: 14px;
                color: var(--color--white);
            }
        }

        &.mzb-heading-layout-2-style-1 {
            margin-bottom: 20px;
            border-bottom: 2px solid #000;

            h2 {
                margin-bottom: 0;

                &::before {
                    all: unset;
                }
            }
        }
    }
}
