.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
    .mzb-post-heading {
        &.mzb-heading-layout-3-style-1 {
            margin-bottom: 20px;
            width: 100%;
            background-color: transparent;
            border-bottom: 5px solid #dfdfdf;
            position: relative;

            &::before {
                content: '';
                position: absolute;
                left: 0;
                bottom: -5px;
                width: 52px;
                border-bottom: 5px solid var(--color--primary);
                box-sizing: border-box;
            }

            h2 {
                display: inline-block;
                padding-left: 2px;
                font-weight: 600;
                font-size: 22px;
				margin-bottom: 12px;

                &::before {
                    all: unset;
                }
            }
        }
    }
}

.mzb-heading {
    &.mzb-heading-layout-3-style-1 {
        margin-bottom: 20px;
        width: 100%;
        background-color: transparent;
        position: relative;

		.mzb-heading-inner {
			margin-bottom: 20px;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -5px;
				width: 52px;
				border-bottom: 5px solid var(--color--primary);
				box-sizing: border-box;
			}
		}

        .mzb-heading-text {
            display: inline-block;
            padding-left: 2px;
            font-weight: 600;
			margin-bottom: 0;

            &::before {
                all: unset;
            }
        }
    }
}
