@import './variables/colors.scss';
@import './variables/structure.scss';
@import './assets/sass/common/layouts/headings/layout-1';
@import './assets/sass/common/layouts/headings/layout-2';
@import './assets/sass/common/layouts/headings/layout-3';
@import './assets/sass/common/layouts/headings/layout-4';
@import './assets/sass/common/layouts/headings/layout-5';
@import './assets/sass/common/layouts/headings/layout-6';
@import './assets/sass/common/layouts/headings/layout-7';
@import './assets/sass/common/layouts/headings/layout-8';
@import './assets/sass/common/layouts/headings/layout-9';
@import './assets/sass/common/pagination/pagination';
@import './assets/sass/common/post-box/border';
@import './assets/sass/common/post-box/boxed';
@import './assets/sass/common/animation';

@import "~swiper/css";
@import "swiper/css/a11y";
@import "swiper/css/autoplay";
@import "~swiper/css/pagination";
@import '~swiper/css/navigation';
@import '~swiper/css/thumbs';

.mzb-post-col--1 {
	--grid-column: 1;
}

.mzb-post-col--2 {
	--grid-column: 2;
}

.mzb-post-col--3 {
	--grid-column: 3;
}

.mzb-icon {
	fill: var(--color--primary);
	height: 14px;
	width: 14px;
}

.mzb-featured-posts,
.mzb-category-list,
.mzb-grid-module,
.mzb-post-list,
.mzb-featured-categories,
.mzb-slider {
	.mzb-post-heading {
		font-size: 18px;
		position: relative;
		line-height: 1.2;
		display: flex;
		align-items: center;

		&.mzb-heading-layout-1-style-1 {
			padding-left: 20px;
			margin-bottom: 10px;

			.mzb-heading-text {
				margin: 0;

				&::before {
					content: '';
					width: 2px;
					height: 100%;
					background-color: var(--color--primary);
					display: inline-block;
					border-radius: 2px;
					position: absolute;
					left: 0;
				}
			}
		}
	}
}

.mzb-grid-module,
.mzb-featured-posts,
.mzb-featured-categories {
	.mzb-post-heading {

		.mzb-heading-text {
			font-size: 24px;
			font-weight: 700;
		}
	}
}

.mzb-post-list {
	.mzb-post-heading {

		.mzb-heading-text {
			font-weight: 500;
		}
	}
}

.comments-link {
	a {
		font-size: 14px;
	}
}

.mzb-post-author {
	a {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		max-width: 66px;
	}

	img {
		height: 24px;
		width: 24px;
		border-radius: var(--mzb-border-radius--xxl);
	}
}

.mzb-post-date {
	svg {
		margin-bottom: 3px;
	}
}

.mzb-post {

	.mzb-post-categories,
	.mzb-post-count {
		a {
			&:hover {
				color: #fff;
				opacity: 0.8;
			}
		}
	}
}

.mzb-featured-posts,
.mzb-featured-categories {
	.mzb-post-categories {
		a {
			&:hover {
				color: var(--color--primary);
			}
		}
	}
}

.mzb-banner-posts,
.mzb-grid-module {
	.mzb-post {
		&-author {
			display: flex;
			align-items: center;
			gap: 8px;

			a {
				max-width: 60px;
				color: var(--color--white);
				font-size: 14px;
				font-weight: var(--mzb-font-weight--regular);
				text-decoration: none;

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}
	}
}

.mzb-featured-posts,
.mzb-featured-categories {
	.mzb-post {
		&-author {
			display: flex;
			align-items: center;
			gap: 8px;

			a {
				max-width: 60px;
				font-size: 14px;
				font-weight: var(--mzb-font-weight--regular);
				text-decoration: none;

				&:hover {
					opacity: 0.8;
				}
			}
		}
	}
}

.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
	.mzb-post-heading {

		.mzb-view-more {
			a {
				p {
					margin: 0;
				}
			}
		}

		&.mzb-heading-layout-1-style-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border: none;
		}
	}
}

.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
	.mzb-post-heading {

		&.mzb-heading-layout-2-style-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 10px;
			border-bottom: 2px solid #000;
			width: 100%;

			.mzb-heading-text {
				display: inline-block;
				background-color: var(--color--black);
				border-radius: 4px 4px 0px 0px;
				padding: 6px 12px;
				color: var(--color--white);
				margin-bottom: 0;

				&::before {
					all: unset;
				}
			}
		}
	}
}

.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
	.mzb-post-heading {
		&.mzb-heading-layout-3-style-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
			width: 100%;
			background-color: transparent;
			border-bottom: 2px solid #dfdfdf;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -2px;
				width: 52px;
				border-bottom: 2px solid var(--color--primary);
				box-sizing: border-box;
			}

			.mzb-heading-text {
				display: inline-block;
				padding-left: 2px;
				margin-bottom: 10px;

				&::before {
					all: unset;
				}
			}
		}
	}
}

.mzb-heading {
	&.mzb-heading-layout-2-style-1 {

		.mzb-heading-text {
			color: var(--color--white);
		}
	}

	&.mzb-heading-layout-3-style-1 {
		padding-bottom: 10px;
		width: 100%;
		background-color: transparent;

		.mzb-heading-inner {
			border-bottom: 2px solid #dfdfdf;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -3px;
				width: 52px;
				border-bottom: 2px solid var(--color--primary);
				box-sizing: border-box;
			}
		}

		.mzb-heading-text {
			display: inline-block;
			padding-left: 2px;

			&::before {
				all: unset;
			}
		}
	}

	&.mzb-heading-layout-8-style-1 {

		.mzb-heading-inner {
			position: relative;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -2px;
				width: 52px;
				border-bottom: 2px solid var(--color--primary);
				box-sizing: border-box;
			}
		}
		.mzb-heading-text {
			display: inline-block;
			padding-left: 2px;

			&::before {
				all: unset;
			}
		}
	}

}

.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
	.mzb-post-heading {

		&.mzb-heading-layout-4-style-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 2px solid #dfdfdf;

			.mzb-heading-text {
				margin-bottom: 10px;
				padding-left: 20px;

				&::before {
					content: "";
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					width: 8px;
					height: 8px;
					background-color: var(--color--primary);
					display: block;
					border-radius: 50%;
				}
			}
		}

		&.mzb-heading-layout-5-style-1 {
			display: flex;
			justify-content: space-between;
			align-items: center;
			border-bottom: 2px solid #dfdfdf;

			.mzb-heading-text {
				margin-bottom: 10px;
				padding-left: 20px;

				&::before {
					content: "";
					position: absolute;
					left: 0;
					top: 50%;
					transform: translateY(-50%);
					width: 8px;
					height: 8px;
					background-color: var(--color--primary);
					display: block;
				}
			}
		}

		&.mzb-heading-layout-7-style-1 {
			display: flex;
			align-items: center;
			margin-bottom: 20px;

			&::after {
				content: '';
				flex: 1;
				border-bottom: 1px solid #ccc;
			}
		}

		&.mzb-heading-layout-8-style-1 {
			padding-bottom: 10px;
			position: relative;

			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: -2px;
				width: 52px;
				border-bottom: 2px solid var(--color--primary);
				box-sizing: border-box;
			}

			.mzb-heading-text {
				display: inline-block;
				padding-left: 2px;
				margin-bottom: 10px;

				&::before {
					all: unset;
				}
			}
		}

		&.mzb-heading-layout-9-style-1 {
			border-top: 2px solid #dfdfdf;
		}
	}
}

@media (max-width: 768px) {

	.mzb-post {

		.mzb-banner-posts .mzb-post-content,
		.mzb-grid-module .mzb-post .mzb-post-title,
		.mzb-grid-module .mzb-post .mzb-entry-summary,
		.mzb-featured-categories .mzb-post-content {
			text-align: center;
		}

		.mzb-banner-posts .mzb-post .mzb-post-entry-meta,
		.mzb-post-list .mzb-post .mzb-post-entry-meta,
		.mzb-post-list .mzb-post,
		.mzb-post-list .mzb-pagination-numbers,
		.mzb-post-list .mzb-pagination-numbers .components-flex,
		.mzb-grid-module .mzb-post .mzb-post-categories,
		.mzb-grid-module .mzb-post .mzb-post-entry-meta,
		.mzb-grid-module .mzb-pagination-numbers,
		.mzb-grid-module .mzb-pagination-numbers .components-flex,
		.mzb-featured-categories .mzb-post .mzb-post-entry-meta {
			justify-content: center;
		}
	}
}
