.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-heading,
.mzb-slider {
    .mzb-post-heading {

        &.mzb-heading-layout-4-style-1 {
            margin-bottom: 20px;
            border-bottom: 4px solid #dfdfdf;

            h2 {
                margin-bottom: 0;
                position: relative;
                padding-left: 20px;

                &::before {
                    all: unset;
                    content: "•";
                    position: absolute;
                    left: 0;
                }
            }
        }
    }
}
