.mzb-news-ticker {
	position: relative;
	height: 50px;
	display: flex;
	align-items: center;

	.mzb-heading {
		margin-right: 10px;
		margin-left: 5px;
	}

	span {
		margin-top: 4px;
		width: auto;
		display: flex;
		line-height: normal;
		height: 100%;
		color: var(--color--primary);
		text-transform: uppercase;
		font-weight: 700;
		font-size: 13px;
		align-items: center;
	}

	svg {
		fill: var(--color--primary);
	}

	.mzb-news-ticker-box {
		padding-top: 2.6px;
		overflow: hidden;
		width: 266px;
		text-align: left;

		&.mzb-news-ticker-arrows {
			height: 100%;
			display: flex;
			align-items: center;
			gap: 1rem;

			.mzb-news-ticker-nav-btn {
				text-decoration: none;
				border: 1px solid #ddd;
				border-radius: 4px;
				padding: 4px 8px;
				cursor: pointer;
				transition: all 0.2s;
				color: inherit;

				svg {
					height: 14px;
					width: 14px;
				}

				&:hover {
					background: #f0f0f0;
				}

				&:focus {
					box-shadow: none;
				}
			}
		}

		ul {
			width: 100px;
			height: 20px;
			list-style-type: none !important;
			padding: 0;
			margin: 0 !important;

			li {
				width: 100%;
				line-height: 1.4;

				a {
					text-decoration: none;
					font-size: 13px;
					color: #000;
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 1;
					-webkit-box-orient: vertical;
				}
			}
		}
	}
}

@media (max-width: 360px) {

	.mzb-news-ticker-box {
		width: 230px;
	}
}