.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
    .mzb-post-heading {

        &.mzb-heading-layout-5-style-1 {
            margin-bottom: 20px;
            border-bottom: 4px solid #dfdfdf;

            h2 {
                margin-bottom: 0;
                position: relative;
                padding-left: 25px;

                &::before {
                    all: unset;
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 8px;
                    height: 8px;
                    background-color: currentColor;
                    display: block;
                }
            }
        }
    }
}
