.mzb-grid-module {

	&.mzb-layout-1 {
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: 1fr;

		&.mzb-layout-1-style-1 {
			.mzb-posts {
				&.mzb-post-col--2 {
					grid-template-columns: repeat(2, 1fr);
				}

				&.mzb-post-col--3 {
					grid-template-columns: repeat(3, 1fr);
				}

				.mzb-post-content {

					.mzb-post-meta {

						.mzb-post-categories,
						.mzb-comments-link {
							color: var(--color--white);

							svg {
								fill: var(--color--white);
							}
						}
					}

					.mzb-post-title {
						color: var(--color--white);
					}

					.mzb-post-view-count,
					.mzb-post-read-time {
						svg {
							fill: var(--color--white);
						}
					}
				}
			}
		}

		&.mzb-layout-1-style-2 {
			.mzb-posts {
				grid-template-columns: 2fr 1fr 1fr 1fr;

				&.mzb-post-col--2 {
					grid-template-columns: 2fr 1fr;
				}

				&.mzb-post-col--3 {
					grid-template-columns: repeat(3, 1fr);
				}

				.mzb-post:first-child {
					grid-column-start: initial;
					grid-column-end: initial;
				}
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-grid-module {

		&.mzb-layout-1 {

			&.mzb-layout-1-style-2 {
				.mzb-posts {
					grid-template-columns: repeat(3, 1fr);

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-column-end: 4;
						order: initial;
					}

					.mzb-post-content {
						.mzb-post-title {
							color: var(--color--white);
						}

						.mzb-post-view-count,
						.mzb-post-read-time {
							svg {
								fill: var(--color--white);
							}
						}
					}
				}
			}
		}
	}
}

@media (min-width: 1200px) {

	.mzb-grid-module {

		&.mzb-layout-1 {
			&.mzb-layout-1-style-2 {
				.mzb-posts {
					grid-template-columns: 2fr 1fr 1fr 1fr;

					.mzb-post:first-child {
						grid-column-start: initial;
						grid-column-end: initial;
					}

					.mzb-post-content {
						.mzb-post-title {
							color: var(--color--white);
						}

						.mzb-post-view-count,
						.mzb-post-read-time {
							svg {
								fill: var(--color--white);
							}
						}
					}
				}
			}
		}
	}
}