.mzb-featured-posts {

	.mzb-layout-2 {

		.mzb-post {
			column-gap: 16px;
			align-items: center;

			&.mzb-first-post--highlight {
				grid-column-start: 1;
				grid-column-end: 2;
				padding: 0;
				align-items: normal;

				.mzb-post-meta {
					display: flex;
				}

				.mzb-featured-image {

					img {
						width: 100%;
					}
				}

				.mzb-post-author {
					display: flex;
				}
			}
		}

		&.mzb-layout-2-style-1 {
			grid-template-columns: 1fr;
			--column-gap: 20px;
			--row-gap: 20px;
			column-gap: var(--column-gap);
			row-gap: var(--row-gap);

			.mzb-post-meta {
				display: none;
			}

			.mzb-featured-image {
				flex: unset;

				img {
					height: 110px;
					width: 110px;
					border-radius: 4px;
				}
			}

			.mzb-post-content {
				padding: 0;

				.mzb-post-title {
					margin-top: 0;
					margin-bottom: 12px;
				}
			}

			.mzb-post-author {
				display: none;
			}
		}
	}
}

@media (min-width: 992px) {

	.mzb-featured-posts {

		.mzb-layout-2 {

			&.mzb-layout-2-style-1 {
				grid-template-columns: 1.4fr 1fr;
			}

			&.mzb-layout-2-style-1 {

				.mzb-post {

					&.mzb-first-post--highlight {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 5;
						padding: 0;
					}
				}
			}
		}
	}
}


@media (max-width: 640px) {
    .mzb-featured-posts {
		.mzb-posts {
			&.mzb-layout-2 {
				&.mzb-layout-2-style-1 {
					.mzb-post {
						&:not(.mzb-first-post--highlight) {
							flex-direction: row;
							align-items: center;
							column-gap: 20px;

							.mzb-featured-image {
								width: auto;
							}

							.mzb-post-content {
								align-self: center;
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 500px) {
	.mzb-featured-posts {
		.mzb-posts {
			&.mzb-layout-2 {
				&.mzb-layout-2-style-1 {
					.mzb-post {
						&:not(.mzb-first-post--highlight) {
							flex-direction: column;

							.mzb-featured-image {
								width: 100%;
								img {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}
}
