.mzb-featured-posts {

    .mzb-layout-3 {

        .mzb-post {
            column-gap: 16px;
            flex-direction: column;

            .mzb-featured-image {
                flex: unset;
				width: 100%;
            }

            &.mzb-first-post--highlight {
                grid-column-start: 1;
                grid-column-end: 2;
                grid-row: span 2;
                padding: 0;

                .mzb-post-meta {
                    display: flex;
                }

                .mzb-featured-image {
					height: 416px;
                    img {
                        width: 100%;
                    }
                }

                .mzb-post-content {
                    padding-top: 30px;
                }

                .mzb-post-author {
                    display: flex;
                }
            }

			&:not(.mzb-first-post--highlight) {
				align-items: normal;
			}
        }

        &.mzb-layout-3-style-1 {
            grid-template-columns: 1.4fr 1fr 1fr;
            --column-gap: 20px;
            --row-gap: 20px;
            column-gap: var(--column-gap);
            row-gap: var(--row-gap);

            .mzb-featured-image {
                flex: unset;

                img {
                    border-radius: 4px;
                }
            }

            .mzb-post-content {
                align-self: auto;
            }

            .mzb-post-author {
                display: none;
            }
        }
    }
}

@media (min-width: 992px) {

    .mzb-featured-posts {

        .mzb-layout-3 {

            &.mzb-layout-3-style-1 {
                grid-template-columns: 1.4fr 1fr 1fr;

                .mzb-post {

                    &.mzb-first-post--highlight {
                        grid-column-start: 1;
                        grid-column-end: 2;
                        grid-row: span 2;
                        padding: 0;
                    }
                }
            }
        }
    }
}

@media (max-width: 768px) {
    .mzb-featured-posts {
		.mzb-posts {
			&.mzb-layout-3 {
				&.mzb-layout-3-style-1 {
					grid-template-columns: 1fr 1fr;

					.mzb-post {
						&.mzb-first-post--highlight {
							grid-column: 1/-1;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 400px) {
    .mzb-featured-posts {
		.mzb-posts {
			&.mzb-layout-3 {
				&.mzb-layout-3-style-1 {
					grid-template-columns: 1fr;
				}
			}
		}
	}
}

