.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
    .mzb-post-heading {

        &.mzb-heading-layout-6-style-1 {
			display: flex;
			align-items: center;
			text-align: center;
			margin-bottom: 20px;

			&::before,
			&::after {
				content: '';
				flex: 1;
				border-bottom: 1px solid #ccc;
			}

			.mzb-heading-text {
				display: inline-block;
				padding: 0 10px;
				font-weight: 400;
				color: var(--color--black);
				margin-bottom: 0;

				&::before {
					all: unset;
				}
			}
		}
	}
}

.mzb-heading {
	 &.mzb-heading-layout-6-style-1 {
		.mzb-heading-inner {
			display: flex;
			align-items: center;
			text-align: center;
			margin-bottom: 20px;

			&::before,
			&::after {
				content: '';
				flex: 1;
				border-bottom: 1px solid #ccc;
				}
		}

		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			display: inline-block;
			padding: 0 10px;
			font-weight: 400;
			color: var(--color--black);
			margin-bottom: 0;

			&::before {
				all: unset;
			}
		}
	}
}
