.mzb-banner-posts {

	.mzb-posts {

		&.mzb-layout-5 {

			&.mzb-layout-5-style-1, &.mzb-layout-5-style-2 {
				grid-template-columns: 1fr;
			}

			&.mzb-layout-5-style-2 {
				all: unset;
				display: grid;
				grid-template-columns: 1fr;

				.mzb-first-post--highlight{
					grid-column: 1 / -1;

					.mzb-featured-image{
						&::before {
							border-radius: 4px;
							background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 35.1%, #000 99.92%);
							position: absolute;
							content: '';
							top: 0;
							left: 0;
							z-index: 1;
							height: 100%;
							width: 100%;
						}
					}
					.mzb-post-content{
						display: flex;
						flex-direction: column;
						align-items: flex-start;
						height: 100%;
						justify-content: center;
						z-index: 1;
					}
				}

				.mzb-bottom-posts-outer-wrapper{
					padding-left: 20px;
					padding-right: 20px;
					z-index: 1;
					display: flex;
				}

				.mzb-bottom-posts-wrapper{
					display:flex;
					justify-content: center;
					flex-direction: column;
					align-items: center;
					gap: var(--gap,20px);
					margin-top: -100px;
					margin-left: auto;
					margin-right: auto;
					flex-wrap: wrap;

					.mzb-post{
						height: calc(var(--height)/2 - var(--gap)/2);
						.mzb-featured-image{
							&::before {
								border-radius: 4px;
								background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 35.1%, #000 99.92%);
								position: absolute;
								content: '';
								top: 0;
								left: 0;
								z-index: 1;
								height: 100%;
								width: 100%;
							}
						}

						.mzb-post-content{
							z-index: 1;
						}

					}
				}
			}

		}
	}

	&.mzb-alignfull{
		margin-left: calc(50% - 50vw);
		margin-right: calc(50% - 50.1vw);
	}
}

@media (max-width: 840px) {
.mzb-banner-posts {
		.mzb-posts {

			&.mzb-layout-5 {
				&.mzb-layout-5-style-2 {
					.mzb-bottom-posts-wrapper{
						flex: 1;
						.mzb-post{
							width: 100%;
						}
					}
				}
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {

			&.mzb-layout-5 {

				&.mzb-layout-5-style-1 {
					all: unset;
					display: grid;
					grid-template-areas:
						"highlighted post-1 post-2"
						"highlighted post-3 post-3";
					grid-template-columns: 2fr 1fr 1fr;
					gap: var(--gap, 20px);

					.mzb-post{
						&:nth-child(1){
							grid-area: highlighted;
						}
						&:nth-child(2){
							grid-area: post-1;
						}
						&:nth-child(3){
							grid-area: post-2;
						}
						&:nth-child(4){
							grid-area: post-3;
						}

						&:nth-child(2),
						&:nth-child(3),
						&:nth-child(4) {
							height: calc(var(--height)/2 - var(--gap)/2);
						}
					}
				}

				&.mzb-layout-5-style-2 {
					.mzb-bottom-posts-wrapper{
						flex-direction: row;
					}
				}
			}
		}
	}
}
