.mzb-banner-posts {

	.mzb-posts {

		&.mzb-layout-4 {

			&.mzb-layout-4-style-1, &.mzb-layout-4-style-2 {
				grid-template-columns: 1fr;
			}

		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {

			&.mzb-layout-4 {

				&.mzb-layout-4-style-1 {
					all: unset;
					display: grid;
					grid-template-areas:
						"post-1 highlighted post-2"
						"post-3 highlighted post-4";
					grid-template-columns: 1fr 2fr 1fr;
					gap: var(--gap, 20px);

					.mzb-post{
						&:nth-child(1){
							grid-area: highlighted;
						}
						&:nth-child(2){
							grid-area: post-1;
						}
						&:nth-child(3){
							grid-area: post-2;
						}
						&:nth-child(4){
							grid-area: post-3;
						}
						&:nth-child(5){
							grid-area: post-4;
						}

						&:nth-child(2),
						&:nth-child(3),
						&:nth-child(4),
						&:nth-child(5) {
							height: calc(var(--height)/2 - var(--gap)/2);
						}
					}
				}

				&.mzb-layout-4-style-2 {
					all: unset;
					display: grid;
					grid-template-areas:
						"highlighted highlighted highlighted2"
						"post-1 post-2 post-3";
					grid-template-columns: repeat(3, 1fr);
					gap: var(--gap, 20px);

					.mzb-post{
						&:nth-child(1){
							grid-area: highlighted;
						}
						&:nth-child(2){
							grid-area: highlighted2;
						}
						&:nth-child(3){
							grid-area: post-1;
						}
						&:nth-child(4){
							grid-area: post-2;
						}
						&:nth-child(5){
							grid-area: post-3;
						}

						&:nth-child(3),
						&:nth-child(4),
						&:nth-child(5) {
							height: calc(var(--height)/2 - var(--gap)/2);
						}
					}
				}
			}
		}
	}
}
