.components-panel__body.mzb-panel-body.is-opened>div:last-child .mzb-advance-select-body svg {
    width: auto;
    height: auto;
}

.editor-styles-wrapper .block-editor-block-list__layout.is-root-container> :where(:not(.alignleft):not(.alignright):not(.alignfull))[data-type="magazine-blocks/icon"] {
    display: flex;
}

.mzb-icon-block {
    display: inline-flex;
    width: auto;

    .mzb-outside-icon {
        display: inline-block;
        position: relative;
    }

    .mzb-icon-wrapper {
        display: flex;

        svg {
            fill: #690aa0;
        }

        &.mzb-fill-preset {
            background-color: #690aa0;
            border-radius: 100%;
            padding: 5px;

            svg {
                fill: #fff;
            }
        }

        &.mzb-outline-preset {
            border-style: solid;
            border-radius: 100%;
            border-color: #690aa0;
            border-width: 2px;
        }

        &.mzb-rectangular-fill-preset {
            background-color: #690aa0;

            svg {
                fill: #fff;
            }
        }

        &.mzb-rectangular-outline-preset {
            border-style: solid;
            border-color: #690aa0;
            border-width: 2px;
        }

        &.mzb-rounded-fill-preset {
            background-color: #690aa0;
            border-radius: 8px;
            padding: 2px;

            svg {
                fill: #fff;
            }
        }

        &.mzb-rounded-outline-preset {
            border-style: solid;
            border-radius: 8px;
            border-color: #690aa0;
            border-width: 2px;
        }
    }

    &.mzb-left {
        margin-right: auto !important;
    }

    &.mzb-center {
        margin-left: auto !important;
        margin-right: auto !important;
    }

    &.mzb-right {
        margin-left: auto !important;
    }
}
