.mzb-tab-post {
	.mzb-tab-controls {
		display: grid;
		grid-template-columns: 1fr 1fr;
		text-align: center;
		border-radius: var(--mzb-border-radius--xs);

		&.mzb-tab-layout-style1 {
			border-bottom: 2px solid #f2f2f2;
			border-radius: 0;

			.mzb-tab-title {
				&.active {
					background-color: var(--color--white);
					position: relative;
					color: unset;

					&::after {
						content: '';
						position: absolute;
						bottom: -2px;
						left: 0;
						width: 100%;
						height: 2px;
						z-index: 1;
						background-color: var(--color--primary);
					}
				}
			}
		}

		.mzb-tab-title {
			font-size: 18px;
			padding: 10px 0;
			background-color: var(--color--white);
			cursor: pointer;

			&.active {
				color: var(--color--white);
				background-color: var(--color--primary);
				font-weight: 500;
			}

		}
	}

	.mzb-posts {
		display: grid;
		row-gap: 10px;
		margin-top: 10px;

		.mzb-post {
			align-items: center;

			&-entry-meta {
				display: flex;
				flex-wrap: wrap;

				&.mzb-meta-separator--circle {
					span:nth-child(odd):not(:last-child)::after {
						content: "●";
						font-size: 0.5em;
						vertical-align: middle;
					}
				}

				&.mzb-meta-separator--square {
					span:nth-child(odd):not(:last-child)::after {
						content: "■";
						font-size: 0.5em;
						vertical-align: middle;
					}
				}

				&.mzb-meta-separator--vertical-dash {
					span:nth-child(odd):not(:last-child)::after {
						content: "|";
						color: #999;
					}
				}

				&.mzb-meta-separator--horizontal-dash {
					span:nth-child(odd):not(:last-child)::after {
						content: "–";
						color: #999;
					}
				}

				&.mzb-meta-separator--outline-circle {
					span:nth-child(odd):not(:last-child)::after {
						content: "○";
						font-size: 0.6em;
						vertical-align: middle;
					}
				}
			}

			&-categories {
				display: inline-flex;
				gap: 10px;
				flex-wrap: wrap;

				a {
					background-color: var(--color--primary);
					padding: 4px 8px;
					border-radius: var(--mzb-border-radius--xxl);
					color: var(--color--white);
					font-weight: var(--mzb-font-weight--semibold);
					font-size: 12px;
					display: inline-block;
					line-height: 14px;
					text-decoration: none;
				}

				&.mzb-large {
					a {
						padding: 17px 25px;
					}
				}

				&.mzb-small {
					a {
						padding: 8px 16px;
					}
				}

				&.mzb-medium {
					a {
						padding: 12px 20px;
					}
				}

				&.mzb-style-outline {
					a {
						background: transparent;
						border: 1px solid var(--color--primary);
					}
				}

				&.mzb-style-plain {
					a {
						background-color: transparent;
						padding: 0;
					}
				}

				.editor-styles-wrapper {
					a {
						color: var(--color--white);
					}
				}
			}

			&-date {
				display: flex;
				gap: 5px;
				align-items: center;
			}

			&-author {
				display: flex;
				gap: 5px;
				align-items: center;

				img {
					all: unset;
					height: 14px;
					width: 14px;
					border-radius: 50%;
				}
			}
		}
	}

	.mzb-featured-image {
		a {
			display: block;
		}
	}

	.mzb-post {
		display: flex;
		background-color: var(--color--white);
		padding: 8px;
		border-radius: var(--mzb-border-radius--xs);
		gap: 15px;

		&-content {
			flex-basis: 75%;
			flex-grow: 1;
			align-self: center;

			.mzb-post-title {
				margin-bottom: 8px;
			}
		}

		img {
			height: 74px;
			min-width: 74px;
			max-width: 74px;
			border-radius: var(--mzb-border-radius--xs);
			display: block;
			object-fit: cover;
		}

		&-entry-meta {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			gap: 8px;
		}

		&-title {

			a {
				font-size: 16px;
				font-weight: 500;
				line-height: 1.2;
				overflow: hidden;
				text-decoration: none;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				font-size: 14px;
				font-weight: var(--mzb-font-weight--regular);
				color: #414141;
				text-decoration: none;

				&:hover {
					color: var(--color--primary);
				}
			}

			svg {
				fill: #414141;
			}
		}
	}

	.no-thumbnail {
		.mzb-post-content {
			padding-top: 7px;
			padding-bottom: 7px;
		}
	}

	&[data-active-tab='popular'] {
		.mzb-posts {
			&[data-posts='latest'] {
				display: none;
			}
		}
	}

	&[data-active-tab='latest'] {
		.mzb-posts {
			&[data-posts='popular'] {
				display: none;
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-tab-post {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:nth-child(odd):not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}

