.mzb-category-list {
    .mzb-posts {
        &.mzb-layout-3 {

			.mzb-post-count-text {
				display: none;
			}

			&.mzb-layout-3-style-1, &.layout-3-style-1 {
				.mzb-post-categories {
					display: flex;
					align-items: center;
					gap: 8px;

					.mzb-list-icon {
						display: flex;
					}
				}
			}

            .mzb-title-wrapper {
                background-image: none !important;
            }

            .mzb-post,
            .mzb-post-count-wrapper {
                all: unset;
                display: flex;
            }

            .mzb-post-count-wrapper {
                a {
                    color: #111;
                    font-weight: 400;
                }
            }

            .mzb-post-count {
                a:nth-child(2) {
                    display: none;
                }
            }

            .mzb-post-categories {
                all: unset;
                padding: 10px;

                a {
                    color: #111;
                    padding: 0;
                    font-weight: 400;
                }
            }

            &.separator {
                .mzb-post {
                    &:not(:last-child) {
                        border-bottom: 1px solid #eaeaea;
                    }
                }
            }
        }
    }
}
