.mzb-modal-popup-wrap {
	.wp-block {
		max-width: none !important;
		margin: 0 !important;
	}
}

.mzb-modal-popup {
	display: none;

	.mzb-modal-popup-close {
		opacity: 1;
		display: flex;
		align-items: center;
		position: absolute;
		text-align: center;
		cursor: pointer;
		fill: #fff;
	}
}

.mzb-inline-editing {
	font-size: 14px;
	font-weight: 500;
	line-height: 1.6;
}

.mzb-button-link {
	background: none;
	&:hover {
		cursor: pointer;
	}

	&.is-style-link {
		border: none;
		text-decoration: underline;
	}

	&.is-style-plain{
		border: none;
	}
}

.mzb-modal-popup-close.window-top-right {
	top: -20px;
	right: -25px;

	svg {
		fill: #fff;
	}
}

.mzb-modal-popup-close.window-top-left {
	top: -20px;
	left: -25px;

	svg {
		fill: #fff;
	}
}

.mzb-button-icon {
	display: flex;
	width: fit-content;

	.mzb-icon {
		fill: #fff;
	}
}

.mzb-modal-popup-wrap {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	box-sizing: border-box;
	background: #fff;
	border-radius: 8px;
	color: #333;
	position: relative;
	z-index: 9999999;
	margin: 30px;
}

.mzb-modal-popup-close {
	display: none;
}

.mzb-modal-popup-content {
	padding: 35px 35px;
	overflow-x: hidden;
	overflow-y: auto;
	height: 100%;
}

.mzb-modal-popup-wrap {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 700px;
	height: 310px;
	max-width: 100%;
	box-sizing: border-box;
	background: #fff;
	color: #333;
	position: relative;
	z-index: 9999999;

	.mzb-modal-popup-content::-webkit-scrollbar {
		width: 5px;
	}

	.mzb-modal-popup-content::-webkit-scrollbar-thumb {
		box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
		border-radius: 100px;
	}
}

.wp-block-mzb-modal {
	width: 100%;
}

.entry {
	.entry-content {
		a.mzb-modal-button-link {
			text-decoration: none;
			align-items: center;
		}

		a.mzb-modal-button-link:hover {
			color: inherit;
		}
	}
}

.mzb-modal-trigger:not(img) {
	display: flex;

	svg {
		font-size: 30px;
		width: 30px;
		height: 30px;
		line-height: 30px;
		cursor: pointer;
	}
}

img.mzb-modal-trigger {
	cursor: pointer;
	height: auto;
	max-width: 100%;
}

.mzb-modal-wrapper {
	.mzb-button {
		line-height: 1;

		a.mzb-modal-button-link.mzb-modal-trigger {
			display: inline-flex;
			text-decoration: none;
			align-items: center;
			cursor: pointer;

			svg {
				fill: currentColor;
				font-style: normal;
				vertical-align: top;
				width: 15px;
				height: 15px;
				font-size: 15px;
			}
		}
	}

	.mzb-button {
		line-height: 1;

		.mzb-button-link {
			border-radius: 2px;
			padding: 12px 25px;
			color: #fcfcfc;
			font-size: 14px;
			line-height: 1.6;
			display: inline-flex;
			align-items: center;
			gap: 4px;

			.mzb-modal-content-wrapper {
				display: flex;
				align-items: center;
			}
		}

		.mzb-button-link.is-small {
			padding: 8px 16px;
		}

		.mzb-button-link.is-medium {
			padding: 12px 20px;
		}

		.mzb-button-link.is-medium {
			padding: 17px 20px;
		}


		.mzb-button-link.is-style-filled {
			background: #7E36F4;
			border: none;
		}

		.mzb-button-link.is-style-link {
			box-shadow: none;

			.mzb-modal-content-wrapper {
				text-decoration: underline;
			}
		}

		.mzb-button-link.is-style-plain {
			box-shadow: none;
		}

		.mzb-button-link.is-style-outline {
			border-color: #7E36F4;
			border-style: solid;
			border-width: 1px;
			box-shadow: 0 0 0 1.5px #7E36F4;
		}

		.mzb-button-link:not(.is-style-filled) {
			color: #7E36F4;
		}

		.mzb-button-link:not(.is-style-filled) .mzb-icon {
			fill: #7E36F4;
		}
	}
}

