.mzb-modal-popup-close {
	opacity: 1;
	display: flex;
	align-items: center;
	position: absolute;
	text-align: center;
	cursor: pointer;
	top: 4px;
	padding: 14px;

	&.popup-top-right {
		right: 10px;
	}

	&.popup-top-left {
		left: 10px;
	}

	&.window-top-left {
		top: -16px;
		left: -40px;
	}

	&.window-top-right {
		top: -16px;
		right: -40px;
	}

	svg {
		fill: #64748b;
	}
}

.mzb-modal-popup {
	display: none;

	&.is-open {
		display: block;
	}
}

.mzb-modal-wrapper .mzb-button.mzb-button-wrapper,
.mzb-modal-wrapper .mzb-button.mzb-button-wrapper .mzb-button-link {
	width: auto !important;
}
