@import './layout-1';
@import './layout-2';
@import './layout-3';
@import './layout-4';
@import './layout-5';
@import './layout-6';
@import './editor.scss';

.mzb-featured-posts {

	.mzb-top-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	.mzb-posts {

		&.mzb-layout-1,
		&.mzb-layout-2,
		&.mzb-layout-3,
		&.mzb-layout-4,
		&.mzb-layout-5
		&.mzb-layout-6 {

			&.mzb-none {
				.mzb-post {
					background-color: transparent;
					border-radius: none;

					.mzb-featured-image{
						img{
							border-radius: 4px;
						}
					}
					.mzb-post-content {
						padding: 0;
					}
				}
			}
		}
	}

	.mzb-view-more {
		display: inline-block;

		>a {
			display: flex;
			align-items: center;
			gap: 5px;

			svg {
				fill: var(--color--primary);
			}

			p {
				margin: 0;
			}
		}
	}

	.mzb-post-col--2 {

		.mzb-post.mzb-first-post--highlight {

			.mzb-post-content {
				padding: 15px;
				width: calc(100% - 30px);
			}
		}
	}

	.mzb-posts {
		display: grid;
		gap: 20px;
		grid-template-columns: repeat(var(--grid-column, 1), 1fr);

		&.mzb-post-col--2 {

			.mzb-first-post--highlight {

				.mzb-featured-image {

					img {
						height: 180px;
					}
				}
			}

		}

		&.mzb-post-col--full{
			grid-template-columns: 1fr;
		}

		&.mzb-layout-1 {
			&.mzb-post-col--3 {
				.mzb-post {
					&:not(.mzb-first-post--highlight) {
						align-items: center;
					}
				}
			}
		}
	}

	.mzb-post-author {

		a {
			color: var(--color--black);
			max-width: 76px;
			pointer-events: auto !important;

			&:hover {
				color: var(--color--primary);
			}
		}
	}

	.mzb-post {
		display: flex;
		background-color: var(--color--white);
		border-radius: var(--mzb-border-radius--xs);
		overflow: hidden;

		&.mzb-first-post--highlight {
			flex-direction: column;

			.mzb-post-content {
				padding-top: 25px;
				align-self: flex-start;
				width: 100%;
				box-sizing: border-box;

				.mzb-post-view-count,
				.mzb-post-read-time {
					display: flex;
					align-items: center;

					span {
						margin-left: 5px;
					}
				}

				svg {
					height: 14px;
					width: 14px;
					fill: #414141;
				}
			}

			.mzb-post-title {

				a {
					-webkit-line-clamp: 2;
				}
			}

			.mzb-featured-image {
				height: 300px;

				img {
					max-width: 100%;
					height: 100%;
				}
			}
		}

		&:not(.mzb-first-post--highlight) {
			column-gap: 20px;
		}

		.mzb-featured-image {

			img {
				display: block;
				width: 100%;
				object-fit: cover;
				height: 160px;
			}

			&.mzb-category--inside-image{
				position: relative;

				.mzb-post-meta{
					position: absolute;
					left: 0;
					bottom: 0;
					right: 0;
					margin: 20px;
				}
			}
		}

		&-meta {
			justify-content: space-between;
			flex-wrap: wrap;
			display: flex;
			gap: 10px;
			margin-bottom: 10px;

			.comments-link {
				display: flex;
				gap: 5px;
				align-items: center;

				svg {
					height: 16px;
					width: 16px;
					fill: var(--color--primary);
				}
			}

			a {
				pointer-events: auto !important;
			}
		}

		&-content {
			align-self: center;
			flex: 1;
			width: 100%;
			box-sizing: border-box;

			.mzb-post-title {
				margin-bottom: 0;

				a {
					display: block;
					line-height: 1.2;
					overflow: hidden;
					text-decoration: none;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					pointer-events: auto !important;

					&:hover {
						color: var(--color--primary);
					}
				}
			}

			.mzb-post-view-count,
			.mzb-post-read-time {
				display: flex;
				align-items: center;

				span {
					margin-left: 5px;
				}
			}

			svg {
				height: 14px;
				width: 14px;
				fill: #414141;
			}
		}

		&-entry-meta {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 8px;

			&.mzb-meta-separator--circle {
				span:not(:last-child)::after {
					content: "●";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--square {
				span:not(:last-child)::after {
					content: "■";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--vertical-dash {
				span:not(:last-child)::after {
					content: "|";
					color: #999;
				}
			}

			&.mzb-meta-separator--horizontal-dash {
				span:not(:last-child)::after {
					content: "–";
					color: #999;
				}
			}

			&.mzb-meta-separator--outline-circle {
				span:not(:last-child)::after {
					content: "○";
					font-size: 0.6em;
					vertical-align: middle;
				}
			}
		}

		&-categories {
			display: inline-flex;
			gap: 10px;
			flex-wrap: wrap;
			z-index: 0;

			a {
				display: inline-block;
				background-color: var(--color--light--primary);
				padding: 4px 8px;
				border-radius: var(--mzb-border-radius--xxl);
				text-decoration: none;
				color: var(--color--primary);
			}

			.editor-styles-wrapper {

				a {
					color: var(--color--black);
				}
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				color: #414141;
				text-decoration: none;
				pointer-events: auto !important;

				&:hover {
					color: var(--color--primary);
				}
			}

			svg {
				fill: #414141;
			}
		}

		&-read-time {
			display: flex;
			gap: 5px;
			align-items: center;
		}

		.mzb-entry-content {
			margin-top: 10px;

			.mzb-entry-summary {
				p {
					margin: 0;
				}
			}
		}

		.mzb-read-more {
			margin-top: 12px;

			a {
				color: #3f3f46;
				line-height: 1.3;
				text-transform: capitalize;
				text-decoration: none;
				opacity: 0.9;
				display: inline-flex;
				align-items: center;
				gap: 5px;
				pointer-events: auto !important;

				&:hover {
					color: var(--color--primary);
				}
			}
		}

		.mzb-post-content {
			&.mzb-meta-position--bottom{
				.mzb-post-entry-meta {
					margin-top: 10px;
				}
			}

			&.mzb-meta-position--top{
				.mzb-post-entry-meta {
					margin-bottom: 10px;
				}
			}
		}

		&:not(.mzb-first-post--highlight) {
			align-items: center;
		}
	}
}

.magazine-post--pos-1 {
	grid-template-columns: repeat(3, 1fr);

	.magazine-post-info {
		grid-column: 2/4;
	}
}

@media (max-width: 768px) {

	.mzb-featured-posts {

		.mzb-posts {
			&.mzb-layout-1 {
				&.mzb-post-col--3 {
					.mzb-post {
						&:not(.mzb-first-post--highlight) {
							flex-direction: column;

							.mzb-featured-image {
								width: 100%;
								img {
									width: 100%;
								}
							}
						}
					}
				}
			}
		}
	}
}

@media (max-width: 640px) {
	.mzb-featured-posts {

		.mzb-post{
			&:not(.mzb-first-post--highlight) {
				column-gap: 0;
				row-gap: 20px;

				.mzb-post-content {
					align-self: flex-start;
				}
			}
		}

		.mzb-posts {
			&.mzb-layout-1 {
				&.mzb-post-col--3 {
					grid-template-columns: repeat(2, 1fr);
				}
			}
		}
	}
}

@media (max-width: 500px) {
	.mzb-featured-posts {
		.mzb-posts {
			&.mzb-layout-1 {
				&.mzb-post-col--3 {
					grid-template-columns: 1fr;
				}
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-featured-posts {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}
