.mzb-banner-posts {

	.mzb-posts {

		&.mzb-layout-2 {

			&.mzb-layout-2-style-1,
			&.mzb-layout-2-style-2 {
				grid-template-columns: 1fr;
			}

			.mzb-post:nth-child(3) {
				--height: calc(100% + var(--gap));
				height: var(--height);
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {

			&.mzb-layout-2 {

				&.mzb-layout-2-style-1 {

					.mzb-post:nth-child(3) {

						.mzb-featured-image {

							img {
								max-height: 100%;
							}
						}
					}
				}

				&.mzb-layout-2-style-1 {
					grid-template-columns: 1fr 2fr 1fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 2;
						grid-row-start: 1;
						grid-row-end: 3;

						.mzb-post-title {

							a {
								font-size: 32px;
							}
						}
					}

					.mzb-post:nth-child(3) {
						grid-column-start: 3;
						grid-row-start: 1;
						grid-row-end: 3;
					}
				}


				&.mzb-layout-2-style-2 {
					grid-template-columns: 1fr 1fr 1fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 2;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post:nth-child(3) {
						grid-column-start: 3;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post-content {
						padding: 20px;
					}
				}

				&.mzb-layout-2-style-3 {
					grid-template-columns: 2fr 1fr;
					grid-template-rows: 1fr 1fr;

					.mzb-post {
						&:first-child {
							grid-column-start: 1;
							grid-row-start: 1;
							grid-row-end: 3;
						}

						&:nth-child(2) {
							grid-column-start: 2;
							grid-row-start: 1;
							grid-row-end: 2;
						}

						&:nth-child(2),
						&:nth-child(3) {
							height: calc(var(--height)/2 - var(--gap)/2);
						}
					}
				}
			}
		}
	}
}