.mzb-banner-posts {

	.mzb-posts {

		&.mzb-layout-3 {

			&.mzb-layout-3-style-1,
			&.mzb-layout-3-style-2 {
				grid-template-columns: 1fr;
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {

			&.mzb-layout-3 {

				&.mzb-layout-3-style-1 {
					grid-template-columns: 1fr 1fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 3;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 2;
						grid-row-start: 1;
						grid-row-end: 3;

						.mzb-post-title {

							a {
								font-size: 32px;
							}
						}
					}
				}

				&.mzb-layout-3-style-2 {
					grid-template-columns: 3fr 2fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 2;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 2;
						grid-row-start: 1;
						grid-row-end: 2;
					}
				}

				&.mzb-layout-3-style-3 {
					grid-template-columns: 7fr 3fr;

					.mzb-post:first-child {
						grid-column-start: 1;
						grid-row-start: 1;
						grid-row-end: 2;
					}

					.mzb-post:nth-child(2) {
						grid-column-start: 2;
						grid-row-start: 1;
						grid-row-end: 2;
					}
				}
			}
		}
	}
}