.mzb-heading {

	.mzb-sub-heading {
		p {
			margin: 0;
		}
	}

	&.mzb-heading-layout-1-style-1 {
		border: none;

		.mzb-post-heading {
			margin-bottom: 20px;
		}

		.mzb-heading-text {
			position: relative;
			padding-bottom: 0;
			display: inline-flex;
			align-items: center;
			padding-left: 20px;
			line-height: 1.2;
						margin-bottom: 0;

			&::before {
				content: "";
				height: 100%;
				border:2px solid var(--color--primary);
				display: inline-block;
				border-radius: 2px;
				position: absolute;
				left: 0;
			}
		}
	}

	&.mzb-heading-layout-1-style-2 {
		.mzb-post-heading {
			margin-bottom: 20px;
		}

		.mzb-heading-text {
			margin-bottom: 0;
		}
	}

	&.mzb-heading-layout-2-style-1 {

		.mzb-heading-inner {
			margin-bottom: 20px;
			border-bottom-style: solid;
			border-bottom-width: 2px;
			border-bottom-color: #000;
		}

		.mzb-heading-text {
			display: inline-block;
			margin-bottom: 0;
			background-color: var(--color--black);
			border-radius: 4px 4px 0px 0px;
			padding: 6px 12px;
			color: var(--color--white);

			&::before {
				all: unset;
			}
		}
	}

	&.mzb-heading-layout-6-style-1 {

		.mzb-heading-inner {
			display: flex;
			align-items: center;
			text-align: center;
			margin-bottom: 20px;

			&::before,
			&::after {
				content: '';
				flex: 1;
				border-bottom: 1px solid #ccc;
			}
		}

		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			display: inline-block;
			padding: 0 10px;
			font-weight: 400;
			color: var(--color--black);
			margin-bottom: 0;

			&::before {
				all: unset;
			}
		}
	}

	&.mzb-heading-layout-4-style-1 {
		padding-bottom: 10px;

		.mzb-heading-inner {
			margin-bottom: 20px;
		}

		.mzb-heading-text {
			margin-bottom: 0;
			position: relative;
			padding-left: 20px;

			&::before {
				all: unset;
				content: "";
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				width: 8px;
				height: 8px;
				background-color: var(--color--primary);
				display: block;
				border-radius: 50%;
			}
		}
	}

	&.mzb-heading-layout-5-style-1 {
		padding-bottom: 10px;

		.mzb-heading-inner {
			margin-bottom: 20px;
		}

		.mzb-heading-text {
			margin-bottom: 0;
			position: relative;
			padding-left: 20px;

			&::before {
				all: unset;
				content: "";
				position: absolute;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				width: 8px;
				height: 8px;
				background-color: var(--color--primary);
				display: block;
			}
		}
	}

	&.mzb-heading-layout-7-style-1 {

		.mzb-heading-inner {
			display: flex;
			align-items: center;
			margin-bottom: 20px;

			&::after {
				content: '';
				flex: 1;
				border-bottom: 1px solid #ccc;
			}
		}

		.mzb-heading-text {
			margin-bottom: 0;
		}
	}

}
