@import './layout-1';
@import './layout-2';
@import './layout-3';
@import './layout-4';
@import './layout-5';

.mzb-banner-posts {

	.mzb-posts {
		display: grid;
		grid-template-columns: 1fr;
		--gap: 20px;
		gap: var(--gap);
		overflow: hidden;
	}

	.mzb-post {
		position: relative;
		--height: calc(100% + var(--gap));
		height: var(--height);
		overflow: hidden;
		border-radius: var(--mzb-border-radius--xs);

		.mzb-featured-image {
			height: 100%;
			pointer-events: none;

			img {
				display: block;
				height: 100%;
				width: 100%;
				transform: scale(1);
				transition: 0.3s ease-in-out;
				object-fit: cover;
			}
		}

		.mzb-entry-header {
			padding: 30px;

			p {
				font-size: 32px;
			}
		}

		&-content {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(180deg,
					rgba(255, 255, 255, 0) 0%,
					rgba(0, 0, 0, 0.6) 100%);
			mix-blend-mode: normal;
			padding: 15px;
			color: var(--color--white);
			display: grid;
			align-items: end;
			grid-template-rows: repeat(1, 1fr);
			height: 100%;

			.mzb-post-title a,
			.mzb-read-more a,
			.mzb-post-meta a {
				pointer-events: auto !important;
			}

			.mzb-post-title {
				margin-top: 10px;
				margin-bottom: unset;
			}

			.mzb-post-view-count,
			.mzb-post-read-time {
				display: flex;
				align-items: center;

				span {
					font-weight: var(--mzb-font-weight--regular);
					margin-left: 5px;
				}
			}

			svg {
				height: 14px;
				width: 14px;
				fill: var(--color--white);
			}
		}

		&-title {

			a {
				color: var(--color--white);
				font-size: 16px;
				font-weight: var(--mzb-font-weight--semibold);
				line-height: 1.2;
				display: block;
				text-shadow: 1px 1px 2px rgb(0 0 0 / 20%);
				overflow: hidden;
				text-overflow: ellipsis;
				text-decoration: none;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}
		}

		&-entry-meta {
			&.mzb-meta-separator--circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "●";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--square {
				span:nth-child(odd):not(:last-child)::after {
					content: "■";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--vertical-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "|";
					color: #999;
				}
			}

			&.mzb-meta-separator--horizontal-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "–";
					color: #999;
				}
			}

			&.mzb-meta-separator--outline-circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "○";
					font-size: 0.6em;
					vertical-align: middle;
				}
			}
		}

		&-categories {
			display: inline-flex;
			gap: 10px;
			flex-wrap: wrap;

			a {
				background-color: var(--color--primary);
				padding: 4px 8px;
				border-radius: var(--mzb-border-radius--xxl);
				color: var(--color--white);
				font-weight: var(--mzb-font-weight--semibold);
				font-size: 12px;
				display: inline-block;
				line-height: 14px;
				text-decoration: none;
			}

			&.mzb-large {
				a {
					padding: 17px 25px;
				}
			}

			&.mzb-small {
				a {
					padding: 8px 16px;
				}
			}

			&.mzb-medium {
				a {
					padding: 12px 20px;
				}
			}

			&.mzb-style-outline {
				a {
					background: transparent;
					border: 1px solid var(--color--primary);
				}
			}

			&.mzb-style-plain {
				a {
					background-color: transparent;
					padding: 0;
				}
			}

			.editor-styles-wrapper {
				a {
					color: var(--color--white);
				}
			}
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				display: block;
				font-weight: 400;
				color: var(--color--white);
				font-size: 14px;
				text-decoration: none;

				&:hover {
					opacity: 0.8;
					color: var(--color--white);
				}
			}

			svg {
				fill: var(--color--white);
			}
		}

		.mzb-post-entry-meta {
			display: flex;
			align-items: center;
			gap: 8px;
			flex-wrap: wrap;
			margin-top: 10px;

		}
	}

	.mzb-featured-image+.mzb-post-content {
		background: linear-gradient(0deg,
				rgba(0, 0, 0, 0.7),
				rgba(0, 0, 0, 0) 60%,
				rgba(0, 0, 0, 0));
		height: 100%;
	}

	.mzb-entry-summary {
		margin-top: 12px;
	}

	.mzb-read-more {
		margin-top: 12px;

		a {
			color: #fff;
			font-size: 16px;
			font-weight: 600;
			line-height: 1.3;
			text-transform: capitalize;
			text-decoration: none;
			opacity: 0.9;
		}
	}
}

@media (min-width: 768px) {

	.mzb-banner-posts {

		.mzb-posts {
			grid-template-columns: 2fr 1fr 1fr;

			> .mzb-post {
				&:first-child {
					.mzb-post-title {
						a {
							font-size: 32px;
						}
					}

					.mzb-post-content {
						padding: 30px;
					}
				}

				&:nth-child(3),
				&:nth-child(4) {
					height: calc(var(--height)/2 - var(--gap)/2);
				}
			}
		}

		.mzb-post:first-child {
			grid-column-start: 1;
			grid-row-start: 1;
			grid-row-end: 3;
		}

		.mzb-post:nth-child(2) {
			grid-column-start: 2;
			grid-row-start: 1;
			grid-row-end: 3;
		}
	}
}

.editor-styles-wrapper {
	.mzb-banner-posts {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:nth-child(odd):not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}

