.mzb-image {
	position: relative;
	transition: all 200ms;
	margin: 0;

	img {
		transition: all 200ms;
		object-fit: cover;
	}

	.hover-effect-zoom-in:hover {
		transform: scale(1.1);
		transition: transform 0.3s ease-in-out;
	}

	.hover-effect-slide:hover {
		transform: translateX(10px);
		transition: transform 0.3s ease-in-out;
	}

	.hover-effect-gray-scale:hover {
		filter: grayscale(100%);
		transition: filter 0.3s ease-in-out;
	}

	.hover-effect-blur:hover {
		filter: blur(5px);
		transition: filter 0.3s ease-in-out;
	}

	.filter-gray-scale {
		filter: grayscale(1);
	}

	.filter-sepia {
		filter: sepia(0.5);
	}

	.filter-saturation {
		filter: saturate(1.5);
	}

	.filter-vintage {
		filter: sepia(0.2) brightness(1.1) contrast(1.3);
	}

	.filter-early-bird {
		filter: contrast(0.9) sepia(0.2);
	}

	.filter-toaster {
		filter: contrast(1.5) brightness(0.9);
	}

	.filter-mayfair {
		filter: contrast(1.1) saturate(1.1);
	}

	&.overlay {
		display: inline-block;

		.overlay-content {
			position: absolute;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			&>h2 {
				text-align: center;
				width: 100%;
			}

			&.top-left {
				top: 0;
				left: 0;
			}

			&.top-center {
				top: 0;
				left: 50%;
				transform: translateX(-50%);
			}

			&.top-right {
				top: 0;
				right: 0;
			}

			&.center-left {
				top: 50%;
				left: 0;
				transform: translateY(-50%);
			}

			&.center-center {
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}

			&.center-right {
				top: 50%;
				right: 0;
				transform: translateY(-50%);
			}

			&.bottom-left {
				bottom: 0;
				left: 0;
			}

			&.bottom-center {
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
			}

			&.bottom-right {
				bottom: 0;
				right: 0;
			}

			.separator {
				border-top-width: 2px;
				border-color: #000;
				width: 100%;
				margin-bottom: 15px;
			}

			.separator.none {
				display: none;
			}

			.separator.solid {
				border-top: 2px solid #000;
			}

			.separator.double {
				border-top: 3px double #000;
			}

			.separator.dashed {
				border-top: 2px dashed #000;
			}

			.separator.dotted {
				border-top: 2px dotted #000;
			}

			.separator.groove {
				border-top: 2px groove #000;
			}

			.separator.inset {
				border: 2px inset #000;
			}

			.separator.outset {
				border: 2px outset #000;
			}

			.separator.ridge {
				border: 2px ridge #000;
			}
		}
	}

	.mask {
		position: relative;
	}

	.mask-circle {
		mask-image: var(--ba-circle-mask);
	}

	.mask-diamond {
		mask-image: var(--ba-diamond-mask);
	}

	.mask-hexagon {
		mask-image: var(--ba-hexagon-mask);
	}

	.mask-rounded {
		mask-image: var(--ba-rounded-mask);
	}

	.mask-blob1 {
		mask-image: var(--ba-blob-1-mask);
	}

	.mask-blob2 {
		mask-image: var(--ba-blob-2-mask);
	}

	.mask-blob3 {
		mask-image: var(--ba-blob-3-mask);
	}

	.mask-blob4 {
		mask-image: var(--ba-blob-4-mask);
	}

	.mask-size-auto {
		mask-size: auto;
	}

	.mask-size-contain {
		mask-size: contain;
	}

	.mask-size-cover {
		mask-size: cover;
	}

	.mask-position-center-top {
		mask-position: center top;
	}

	.mask-position-center-center {
		mask-position: center center;
	}

	.mask-position-center-bottom {
		mask-position: center bottom;
	}

	.mask-position-left-top {
		mask-position: left top;
	}

	.mask-position-left-center {
		mask-position: left center;
	}

	.mask-position-left-bottom {
		mask-position: left bottom;
	}

	.mask-position-right-top {
		mask-position: right top;
	}

	.mask-position-right-center {
		mask-position: right center;
	}

	.mask-position-right-bottom {
		mask-position: right bottom;
	}

	.mask-repeat-no-repeat {
		mask-repeat: no-repeat;
	}

	.mask-repeat-repeat {
		mask-repeat: repeat;
	}

	.mask-repeat-repeat-x {
		mask-repeat: repeat-x;
	}

	.mask-repeat-repeat-y {
		mask-repeat: repeat-y;
	}
}

.mzb-image:hover .show-heading-hover h2 {
	display: block;
}

.mzb-image .show-heading-hover h2 {
	display: none;
}

.mzb-image:hover .show-description-hover figcaption {
	display: block;
}

.mzb-image .show-description-hover figcaption {
	display: none;
}

.mzb-image:hover .overlay-content .separator.hover {
	opacity: 1;
}

.mzb-image .overlay-content .separator.hover {
	opacity: 0;
}