@import './layout-1';
@import './layout-2';
@import './layout-3';

.mzb-category-list {
	.mzb-posts {
		display: grid;
		row-gap: 5px;
	}

	.mzb-post {
		display: flex;
		overflow: hidden;
		border-radius: var(--mzb-border-radius--xs);
		height: 60px;
	}

	.mzb-title-wrapper {
		flex: 1 0 60%;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
		display: flex;
		align-items: center;
		background-size: cover;
		position: relative;
	}

	.mzb-post-categories {
		position: absolute;
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		display: flex;
		z-index: 1;
	}

	.mzb-post-categories,
	.mzb-post-count {
		a {
			color: var(--color--white);
			font-size: 16px;
			font-weight: var(--mzb-font-weight--semibold);
			text-decoration: none;
			overflow: hidden;
		}
	}

	.mzb-post-categories {
		a {
			padding: 20px 12px;
			width: 100%;

			&:hover {
				opacity: 0.8;
				color: var(--color--white);
			}
		}
	}

	.mzb-post-count-wrapper {
		background-color: var(--color--primary);
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px 18px;
		flex-basis: 100px;
	}

	.mzb-post-count {
		margin: 0;
	}

	.mzb-layout-1-style-2, .layout-1-style-2 {
		.mzb-post-count-wrapper {
			padding: 0;
			background: none;
		}

		.mzb-title {
			display: flex;
			background-color: var(--color--primary);
			width: 100%;
			justify-content: space-between;
			padding: 6px 20px;
		}

		.mzb-title-wrapper {
			align-items: flex-end;
		}

		.mzb-post {
			img {
				height: 120px;
			}
		}

		.mzb-post-categories {
			position: unset;
			background: none;
			align-items: flex-end;
			width: 50%;

			a {
				padding	: 0;
			}
		}
	}

	.mzb-layout-1-style-3, .layout-1-style-3 {
		.mzb-post {
			justify-content: space-between;
			position: relative;
		}

		.mzb-post {

			img {
				height: 90px;
			}
		}

		.mzb-title-wrapper {
			flex: unset;
			width: 100%;
		}

		.mzb-post-count-wrapper {
			position: absolute;
			width: fit-content;
			z-index: 1;
			right: 20px;
			top: 50%;
			transform: translateY(-50%);
			background: none;
			padding: 0;
		}
	}
}

