.mzb-grid-module,
.mzb-featured-categories,
.mzb-featured-posts,
.mzb-post-list,
.mzb-category-list,
.mzb-slider {
    .mzb-post-heading {

        &.mzb-heading-layout-7-style-1 {
            display: flex;
            align-items: center;
            margin-bottom: 20px;

            &::after {
                content: '';
                flex: 1;
                border-bottom: 1px solid #ccc;
            }
        }
    }
}

.mzb-heading {
    &.mzb-heading-layout-7-style-1 {

        .mzb-heading-inner {
            display: flex;
            align-items: center;
            margin-bottom: 20px;

            &::after {
                content: '';
                flex: 1;
                border-bottom: 1px solid #ccc;
            }
        }

    }
}

.mzb-heading-text {
	margin-bottom: 0;
}
