.mzb-featured-posts {

	.mzb-layout-6 {

		&.mzb-posts{
			grid-template-columns: 1fr;
		}

		.mzb-post {
			align-items: center;

			&.mzb-first-post--highlight {
				display: flex;
				flex-direction: column;

				.mzb-featured-image {
					flex: unset;
					height: 400px;

					img{
						border-radius: 10px;
						height: 100%;
					}
				}

				.mzb-post-content {
					align-self: normal;
					padding: 25px 0px;
					width: 100%;
				}
			}

			&:not(.mzb-first-post--highlight){
				gap: 15px;

				.mzb-featured-image{
					flex: none;
					img{
						height: 200px;
						border-radius: 10px;
					}
				}

				.mzb-post-content{
					align-self: center;
					padding: 0;
					.mzb-post-title{
						a{
							font-size: 16px;
						}
					}
				}
			}
		}
	}
}

@media (min-width: 768px) {

	.mzb-featured-posts {

		.mzb-layout-6 {
			&.mzb-posts{
				grid-template-columns: repeat(2, 1fr);
			}

			.mzb-post {
				&.mzb-first-post--highlight {
					flex-direction: row;
					grid-column: span 2;

					.mzb-featured-image {
						height: 500px;
						flex: 1;
					}

					.mzb-post-content {
						align-self: center;
						padding: 25px;
					}
				}

				&:not(.mzb-first-post--highlight){

					.mzb-featured-image{
						img{
							height: 130px;
						}
					}
				}
			}
		}
	}
}

@media (min-width: 992px) {

	.mzb-featured-posts {

		.mzb-layout-6 {
			&.mzb-posts{
				grid-template-columns: repeat(3, 1fr);
			}

			.mzb-post {
				&.mzb-first-post--highlight {
					flex-direction: row;
					grid-column: span 3;

					.mzb-post-content {
						align-self: center;
						padding: 25px;
					}
				}
			}
		}
	}
}

@media (max-width: 768px) {

	.mzb-featured-posts {

		.mzb-layout-6 {

			.mzb-post {
				&.mzb-first-post--highlight {

					.mzb-featured-image {
						width: 100%;
					}
				}
			}
		}
	}
}


@media (max-width: 500px) {
	.mzb-featured-posts {
		.mzb-layout-6 {
			.mzb-post {
				.mzb-post-meta {
					justify-content: center;
				}

				.mzb-post-content {
					text-align: center;
				}

				.mzb-post-entry-meta {
					justify-content: center;
				}

			}
		}

	}
}
