.mzb-featured-categories {

	.mzb-top-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	.mzb-view-more {
		display: inline-block;

		>a {
			display: flex;
			align-items: center;
			gap: 5px;

			svg {
				fill: var(--color--primary);
			}

			p {
				margin: 0;
			}
		}
	}

	.mzb-category-posts {
		display: flex;
		gap: 20px;
	}

	.mzb-post-heading {

		h2 {
			margin-bottom: 0;
		}
	}

	.mzb-category-1-posts,
	.mzb-category-2-posts {
		width: 50%;
		display: flex;
		flex-direction: column;
		gap: 15px;
	}

	.mzb-post-title {

		a {
			display: block;
			font-size: 16px;
			font-weight: var(--mzb-font-weight--semibold);
			text-decoration: none;
			line-height: 1.2;
			overflow: hidden;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;

			&:hover {
				color: var(--color--primary);
			}
		}
	}

	.mzb-post {
		display: flex;
		background-color: var(--color--white);
		border-radius: var(--mzb-border-radius--xs);
		padding: 8px;
		gap: 15px;
		overflow: hidden;

		&:nth-child(2) {
			flex-direction: column;
			padding: unset;
			gap: 0;

			.mzb-featured-image {
				img {
					height: 180px;
					max-width: 100%;
				}
			}

			.mzb-post-content {
				margin-left: unset;
				padding: 15px;
				align-self: flex-start;
				width: calc(100% - 30px);

				.mzb-post-title {
					margin-top: 15px;
					margin-bottom: 15px;

					a {
						font-size: 18px;
					}
				}
			}

			.mzb-post-meta {
				display: flex;
				justify-content: space-between;
			}

			.mzb-post-author {
				a {
					color: var(--color--black);

					&:hover {
						color: var(--color--primary);
					}
				}
			}
		}

		.mzb-featured-image {

			img {
				display: block;
				width: 100%;
				object-fit: cover;
				border-radius: var(--mzb-border-radius--xs);
				height: 74px;
				min-width: 74px;
				max-width: 74px;
			}
		}

		&-meta {
			display: none;

			.comments-link {
				display: flex;
				gap: 5px;
				align-items: center;

				svg {
					height: 16px;
					width: 16px;
					fill: var(--color--primary);
				}
			}
		}

		&-content {
			align-self: center;

			.mzb-post-title {
				margin-bottom: 10px;
			}
		}

		&-entry-meta {
			&.mzb-meta-separator--circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "●";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--square {
				span:nth-child(odd):not(:last-child)::after {
					content: "■";
					font-size: 0.5em;
					vertical-align: middle;
				}
			}

			&.mzb-meta-separator--vertical-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "|";
					color: #999;
				}
			}

			&.mzb-meta-separator--horizontal-dash {
				span:nth-child(odd):not(:last-child)::after {
					content: "–";
					margin: 0 8px;
					color: #999;
				}
			}

			&.mzb-meta-separator--outline-circle {
				span:nth-child(odd):not(:last-child)::after {
					content: "○";
					font-size: 0.6em;
					vertical-align: middle;
				}
			}
		}

		&-categories {
			display: inline-flex;
			gap: 10px;
			flex-wrap: wrap;

			a {
				display: inline-block;
				padding: 4px 8px;
				background-color: var(--color--light--primary);
				color: var(--color--primary);
				font-weight: var(--mzb-font-weight--semibold);
				font-size: 13px;
				border-radius: 18px;
				text-decoration: none;
			}

			.editor-styles-wrapper {

				a {
					color: var(--color--black);
				}
			}
		}

		&-author {
			display: none;
		}

		&-date {
			display: flex;
			gap: 5px;
			align-items: center;

			a {
				font-size: 14px;
				font-weight: var(--mzb-font-weight--regular);
				text-decoration: none;
				color: #414141;

				&:hover {
					color: var(--color--primary);
				}
			}

			svg {
				fill: #414141;
			}
		}

		&-entry-meta {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			gap: 8px;
		}
	}

	.mzb-entry-summary {
		margin-top: 12px;
	}

	.mzb-read-more {
		margin-top: 12px;

		a {
			color: #3f3f46;
			font-size: 16px;
			font-weight: 600;
			line-height: 1.3;
			text-transform: capitalize;
			text-decoration: none;
			opacity: 0.9;

			&:hover {
				color: var(--color--primary);
			}
		}
	}
}

@media (max-width: 768px) {

	.mzb-featured-categories {

		.mzb-post {
			flex-direction: column;
			align-items: center;

			.mzb-featured-image {
				width: 100%;
				img{
					width: 100% !important;
					max-width: 100% !important;
				}
			}

			.mzb-post-content {
				width: 100%;
			}
		}

		.mzb-category-posts {
			flex-direction: column;

			.mzb-category-1-posts,
			.mzb-category-2-posts {
				width: 100%;
			}
		}
	}
}

.editor-styles-wrapper {
	.mzb-featured-categories {
		.mzb-post {
			.mzb-post-content {
				.mzb-post-entry-meta {
					&.mzb-meta-separator--circle {
						span:nth-child(odd):not(:last-child)::after {
							content: "●";
							font-size: 0.8em;
							vertical-align: middle;
						}
					}
				}

			}
		}
	}
}

