.mzb-post-content-block {
	p {
		line-height: 1.7;
		margin-bottom: 1.5rem;
	}

	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-top: 2rem;
		margin-bottom: 0.7rem;
		font-weight: 600;
	}

	ol,
	ul {
		margin-bottom: 1.5rem;
	}

	ol li,
	ul li {
		margin-bottom: 0.5rem;
	}

	ul {
		list-style: disc inside;
	}

	ol {
		list-style: decimal inside;
	}
}
